import { Component, OnInit, ViewChild, Output, EventEmitter} from '@angular/core';
import { RestService } from '../../rest.service';
import { Observable } from 'rxjs';
import { MatTableDataSource, MatSort, MatPaginator } from '@angular/material';
import { CodeTypes } from '../../Enum/CodeTypes';
import { EventTypes } from '../../Enum/EventTypes';
import { FormatNumber } from '../../Funciones';
import { LoadingController } from '../../Controllers/LoadingController';
import { Data } from '../../Charts/donut-chart/donut-chart.component';


export interface evento
{
	id: number;
	fecha : Date;
	terminal : string;
}

export interface row
{
	codigo : string;
	entrada : evento;
	pagos : Array<evento>;
	salida : evento;
}

@Component({
  selector: 'app-tickets',
  templateUrl: './tickets.component.html',
  styleUrls: ['./tickets.component.css']
})

export class TicketsComponent implements OnInit 
{
	@ViewChild(MatPaginator, {static: true}) paginator: MatPaginator;
	@ViewChild(MatSort, {static: true}) sort: MatSort;

	Meses : Array<any> = 
	[
		{ valor: 0, nombre: "Enero", nombreCorto: "Ene", selected: false },
		{ valor: 1, nombre: "Febrero", nombreCorto: "Feb", selected: false },
		{ valor: 2, nombre: "Marzo", nombreCorto: "Mar", selected: false },
		{ valor: 3, nombre: "Abril", nombreCorto: "Abr", selected: false },
		{ valor: 4, nombre: "Mayo", nombreCorto: "May", selected: false },
		{ valor: 5, nombre: "Junio", nombreCorto: "Jun", selected: false },
		{ valor: 6, nombre: "Julio", nombreCorto: "Jul", selected: false },
		{ valor: 7, nombre: "Agosto", nombreCorto: "Ago", selected: false },
		{ valor: 8, nombre: "Septiembre", nombreCorto: "Sep", selected: false },
		{ valor: 9, nombre: "Octubre", nombreCorto: "Oct", selected: false },
		{ valor: 10, nombre: "Noviembre", nombreCorto: "Nov", selected: false },
		{ valor: 11, nombre: "Diciembre", nombreCorto: "Dic", selected: false }
	];

  	NombreDias : Array<any> =
  	[
	    { valor: 0, nombre: "Dom"},
	    { valor: 1, nombre: "Lun"},
	    { valor: 2, nombre: "Mar"},
	    { valor: 3, nombre: "Mié"},
	    { valor: 4, nombre: "Jue"},
	    { valor: 5, nombre: "Vie"},
	    { valor: 6, nombre: "Sáb"}
  	];

	calendario = 
	{
    	hide: true,
    	fechaI : new Date(),
    	fechaF : new Date(),
		top : 200,
		left: 150
  	};

	FormatNumber = new FormatNumber();

	Entradas : Data;
	displayedColumns: string[] = ['codigo', 'entrada', 'pago', 'salida'];
	dataSource : MatTableDataSource<row>;

	constructor(private rest: RestService, private LoadingController: LoadingController) { }
	GetShortDate(fecha : Date) : string
	{
    	let f:Date = new Date(fecha);
	    return this.NombreDias[f.getDay()].nombre + "., " + f.getDate() + " " + this.Meses[f.getMonth()].nombreCorto + ". "+ f.getFullYear();
	}

	GetTime(fecha: Date) : string
	{
		let f:Date = new Date(fecha);
	    return f.toLocaleString('en-US', { hour: 'numeric', minute: 'numeric', hour12: true });
	}

	MostrarCalendario(e)
	{
		this.calendario.hide = false;
		this.calendario.left = e.clientX;
		this.calendario.top = e.clientY + 80;
    	
	}

	CerrarCalendario()
  	{
    	this.calendario.hide = true;
  	}

  	AplicarFechas(data)
  	{
    	this.calendario.fechaI = data.fechaI;
    	this.calendario.fechaF = data.fechaF;
    	this.CargarReporte();
    	this.calendario.hide = true;
  	}

	ngOnInit() 
	{
      	this.calendario.fechaI.setHours(0,0,0,0);
      	this.calendario.fechaF.setHours(23,59,59,59);

		this.CargarReporte();
	}

	Decimal(hexadecimal: string) : number
	{
		return	parseInt(hexadecimal, 16);
	}

	getEventType(et): string
	{
		return EventTypes[et];
	}

	getEventStatus(status):string
	{
		return 'led big ' + ((status == '1')?'gray':'red');
	}

	getCodeType(ct): string
	{
		return CodeTypes[ct];
	}

	getPropertyByPath(obj: Object, pathString: string)
	{
		return pathString.split('.').reduce((o, i) => o[i], obj);
	}

	getShortDate(date):string
	{
		return new Date(date).toLocaleString("es-ES", 
		{ 
		  year: '2-digit', 
		  month: 'short', 
		  day: '2-digit', 
		  hour: '2-digit',  
		  minute: '2-digit',
		  hour12: true 
		}); 
	}

	getLongDate(date):string
	{
		return new Date(date).toLocaleString("es-ES", 
		{ 
		  year: 'numeric', 
		  month: 'long', 
		  day: 'numeric', 
		  hour: '2-digit',  
		  minute: '2-digit',
		  second: '2-digit',
		  weekday: 'long' 
		}); 
	}
	
	CargarReporte()
	{
		this.LoadingController.Mostrar();
		let date : Date = this.calendario.fechaI;
		this.rest.getReporteTicket(date.getFullYear()+"-"+ ((date.getMonth()+1) < 10 ? '0'+((date.getMonth()+1)):(date.getMonth()+1))+"-"+(date.getDate() < 10 ? '0'+date.getDate() : date.getDate())).subscribe((data : any) =>
		{
			let tmp : Array<row> = (data.report != null ? data.report.map(r => 
			{ 
				let row : row = 
				{
					codigo: r.code,
					entrada:
					{
						id:r.idEntarda,
						fecha: r.fechaEntrada != null ? new Date(r.fechaEntrada) : null,
						terminal: r.terminalEntarda
					},
					pagos: 
					[
						{
							id: r.idPago,
							fecha: r.fechaPago != null ? new Date(r.fechaPago) : null,
							terminal: r.terminalPago
						}
					],
					salida:
					{
						id:r.idSalida,
						fecha: r.fechaSalida != null ? new Date(r.fechaSalida) : null,
						terminal: r.terminalSalida
					}
				}; 
				return row; 
			}) : []);

			tmp = tmp.filter((v,i,a)=>
			{
				{
					if(!(a.indexOf(a.find(r => r.entrada.id == v.entrada.id)) == i))
					{
						a.find(r => r.entrada.id == v.entrada.id).pagos.push(v.pagos[0]);
						return false;
					}
					return true;
				}
			});

			this.dataSource = new MatTableDataSource(tmp);
			this.dataSource.sort = this.sort;
      		this.dataSource.paginator = this.paginator;
			this.dataSource.sortingDataAccessor = (data, sortHeaderId: string) => 
			{
				var direction = this.dataSource.sort.direction;
				
				if(sortHeaderId != 'pago')
				{
					for(var p = 0; p < data.pagos.length; p++)
					{
						var pago = data.pagos[p];
						var element = document.getElementById('E' + data.entrada.id + 'P' + pago.id);
						if(element != null) element.style.opacity = '1';
					}
				}

				switch (sortHeaderId) 
				{
					case "entrada": return data.entrada.fecha; break;
					case "salida": return data.salida.fecha; break;
					case "pago": 
					if(data.pagos.length > 0)
					{
						if( direction != 'asc') 
						{
							let miliseconds : Array<Number> =  data.pagos.map(p => p.fecha != null ? p.fecha.getTime() : null);
							var max = Math.max.apply(null, miliseconds);
							var pago = data.pagos.find(p => p.fecha != null ? p.fecha.getTime() : null == max);

							var pagos = data.pagos.filter(p => p != pago);

							for (var p = 0; p < pagos.length; p++) 
							{
								var tmp = pagos[p];
								var element = document.getElementById('E' + data.entrada.id + 'P' + tmp.id);
								if(element != null) element.style.opacity = '0.2';
							}
							
							if( pago != null)
							{
								var element = document.getElementById('E' + data.entrada.id + 'P' + pago.id);
								if(element != null) element.style.opacity = '1';
							}
						}
						else 
						{
							let miliseconds : Array<number> =  data.pagos.map(p => p.fecha != null ? p.fecha.getTime() : null);
							var min = Math.min.apply(null, miliseconds);
							var pago = data.pagos.find(p => p.fecha != null ? p.fecha.getTime() : null == min);

							var pagos = data.pagos.filter(p => p != pago);

							for (var p = 0; p < pagos.length; p++) 
							{
								var tmp = pagos[p];
								var element = document.getElementById('E' + data.entrada.id + 'P' + tmp.id);
								if(element != null) element.style.opacity = '0.2';
							}

							if( pago != null)
							{
								var element = document.getElementById('E' + data.entrada.id + 'P' + pago.id);
								if(element != null) element.style.opacity = '1';
							}
						}
						return pago != null ? pago.fecha : null;
					}
					else return  null; 
					break;
					default: return this.getPropertyByPath(data, sortHeaderId); break;
				}
			};


			this.CountEntradas();
			this.LoadingController.Cerrar(data.message);
		},
		(ex) =>
	    {
	      var mensaje = 
	      {
	        mostrar: true,
	        mensaje: ex.error.message,
	        status: 1
	      };
	      localStorage.setItem("mensaje",JSON.stringify(mensaje));
	      this.LoadingController.Cerrar(ex.statusText);
	    });
	}

	CountEntradas()
	{

		let terminales : Array<string> = this.dataSource.data.map(d => d.entrada.terminal).filter((v, i, a) => a.indexOf(v) == i);
		/*
		{
            Titulo: this.getTipo(tipos[t].tipo, tipos[t].tarifa) + " "+ this.FormatNumber.nuevo(p, '') +"%",
            Porcentaje: p,
            Valor: this.FormatNumber.nuevo(valor, '$'),
            evento: tipos[t]
      	}*/

      	var Total = this.dataSource.data.filter(d=> d.entrada.fecha != null).length;

		this.Entradas = 
		{
			Titulo : "Total de Entradas",
			Valor : Total,
			Segmentos : []
		};
		
		for(var t = 0; t < terminales.length ; t++)
		{
			var terminal = terminales[t];

			var subTotal = this.dataSource.data.filter(d => d.entrada.terminal == terminal).length;
			var porcentaje = (100/Total) * subTotal;
			this.Entradas.Segmentos.push(
			{
				Titulo : 'En ' + terminal,
				Porcentaje : porcentaje,
				Valor: subTotal,
				Selected: false
			})
		}
	}
}
