	recargarTocken():void
	{
		var usuario = JSON.parse(localStorage.getItem("Usuario"));
		this.httpOptions.headers= new HttpHeaders(
		{
    		"Content-Type" : "application/json; charset=utf-8",
    		"x-auth-token" : usuario.Token,
			"user" : usuario.Value
    	})
	}

	BotonPutPension(Pension : Pension, Rate : Rate, IdPerson : number): Observable<{}>
	{
		var response = this.http.put(this.endpointBoton + "Pension", 
			{
				pension:
				{
					user : 
					{
						id : Pension.IdBoton
					},
					parkingid : 4,
					cdsUserId : IdPerson,
					cdsRateId : Rate.Id,
					hasAntiPassBack : true,
					updateDate : new Date(),
					endDate : Pension.Vencimiento,
					startDate : Pension.Comienzo,
					expirationDate : Pension.Vencimiento
				}

			}, this.httpOptionsBoton).pipe(map(this.extractData))
		return response;
	}

