import { Injectable } from '@angular/core';
import { ActivatedRouteSnapshot, RouterStateSnapshot, UrlTree } from '@angular/router';
import { Observable } from 'rxjs';
import { CanActivate, Router } from '@angular/router'
import { RestService } from '../rest.service';

@Injectable({
  providedIn: 'root'
})

export class LoginGuard implements CanActivate {
  constructor(private router: Router, private rest : RestService) {}

  canActivate(data): boolean
  {
    this.SSL();
    //return true;
  	let usuario = localStorage.getItem('Usuario');
  	if(usuario)
  	{
      let Id : number = JSON.parse(usuario).Id;
      let permisos = JSON.parse(localStorage.getItem('Permisos'));
      if(permisos == null)
      {
        if(Id != null)
        {
          this.CargarPermisos(Id);
        }
        else 
        {
          return false;
        }
      }
      else
      {
        console.log(data.data.permiso);
        if(data.data.permiso != null)
        {
          this.CargarPermisos(Id);
          let res : boolean = permisos.includes(data.data.permiso);
          if(!res) 
          {
            this.CambiarURL(data.data.permiso);
          }

          return res;
        }
        else
        {
          return true;
        }
      }
  	}
  	else
  	{
  		this.router.navigate(['./Login']);
  		return false;
  	}
  }

  CambiarURL(permiso : string): void
  {
    let permisos = JSON.parse(localStorage.getItem('Permisos'));

    switch (permiso) {
      case "REPING":
        if(permisos.includes("REPTCK"))
        {
          this.router.navigate(['./Reportes/Tickets']);
        }
      break;

      case "REPTCK":
        if(permisos.includes("REPING"))
        {
          this.router.navigate(['./Reportes/Ingresos']);
        }
      break;

      case "ESTAFL":
        if(permisos.includes("ESTING"))
        {
          this.router.navigate(['./Estadisticas/Ingresos']);
        }
      break;

      case "ESTING":
        if(permisos.includes("ESTAFL"))
        {
          this.router.navigate(['./Estadisticas/Afluencia']);
        }
      break;
      
      default: break;
    }
  }

  CargarPermisos(Id : number) : void
  {
    this.rest.PermisosAsignados(Id).subscribe((result : any)=>
    {
      if(result.success)
      {
        let permisos : string[] = result.Permisos;
        localStorage.setItem('Permisos', JSON.stringify(permisos));
      }
    }); 
  }

  SSL() : void
  {
    let fullUrl : string = window.location.href;
    let protocolo : string = fullUrl.split(':')[0];
    let isLocal : boolean = fullUrl.includes('localhost');

    if(!isLocal)
    {
      fullUrl = protocolo == 'http' ? fullUrl.replace('http', 'https') : fullUrl;
      if(protocolo == 'http')
      {
        window.location.href = fullUrl;
      }
    }
  }
}
