import { Component, OnInit, ViewChild, ElementRef, AfterViewInit } from '@angular/core';
import { Data } from '../../../Charts/area-chart/area-chart.component';
import { LoadingController } from '../../../Controllers/LoadingController';
import { RestService } from '../../../rest.service';
import { estadisticas } from '../../../Models/Interfaces';
import { Meses, Mes } from '../../../Models/Interfaces';
import { FormatNumber } from '../../../Funciones';


@Component({
  selector: 'app-estadisticas-ingresos',
  templateUrl: './estadisticas.ingresos.component.html',
  styleUrls: ['./estadisticas.ingresos.component.css']
})

export class EstadisticasIngresosComponent implements OnInit, AfterViewInit {
	@ViewChild('DaysChart', { static: false }) public DaysChart: ElementRef;
	@ViewChild('WeekChart', { static: false }) public WeekChart: ElementRef;

  @ViewChild('EsteMes', { static: false }) public EsteMesElement: ElementRef;
  @ViewChild('EsteAno', { static: false }) public EsteAnoElement: ElementRef;
  @ViewChild('EstaSemana', { static: false }) public EstaSemanaElement: ElementRef;

  FormatNumber : FormatNumber = new FormatNumber();

  NombreDias : Array<any> =
  [
    { valor: 6, nombre: "Dom"},
    { valor: 0, nombre: "Lun"},
    { valor: 1, nombre: "Mar"},
    { valor: 2, nombre: "Mié"},
    { valor: 3, nombre: "Jue"},
    { valor: 4, nombre: "Vie"},
    { valor: 5, nombre: "Sáb"}
  ];

	Meses : Array<Mes> = Meses;
	Anos : Array<number> = [];

  EsteMes : number = 0;
  EsteAno : number = 0;
  EstaSemana : number = 0;

	Mes : Mes = this.MesActual();
	Ano : number = null;

	Estadisticas : Array<estadisticas> = [];

	Vistas : Array<any> =
	[
		{valor: 0, nombre: 'Mes'}, 
		{valor: 1, nombre: 'Día'}
	]

	vista : number = 1;

	Data : Array<Data> = [
	    {valueX:5, valueY: 'L', minValueX:4, maxValueX: 10, selected: false},
	    {valueX:15, valueY: 'M', minValueX:5, maxValueX: 20, selected: false},
	    {valueX:20, valueY: 'X', minValueX:10, maxValueX: 30, selected: false},
	    {valueX:16, valueY: 'J', minValueX:5, maxValueX: 20, selected: false},
	    {valueX:25, valueY: 'V', minValueX:5, maxValueX: 20, selected: false},
	    {valueX:20, valueY: 'S', minValueX:8, maxValueX: 25, selected: false},
	    {valueX:7, valueY: 'D', minValueX:4, maxValueX: 30, selected: false}
  	];

  	DataByDays : Array<Data> = [];

  	DaysChartAncho : number = 200;
  	DaysChartAlto : number = 200;

  	WeekChartAncho : number = 200;
  	WeekChartAlto : number = 200;

  constructor(private LoadingController : LoadingController, private rest : RestService) { }

  ngOnInit() {
  	this.CargarTemporales();
  	this.CargarEstadisticas();
  }

  ngAfterViewInit() 
  {
  	setTimeout(() => {
  		this.DaysChartAncho = this.DaysChart.nativeElement.offsetWidth - 20;
		this.DaysChartAlto = this.DaysChart.nativeElement.offsetHeight - 100;

		this.WeekChartAncho = this.WeekChart.nativeElement.offsetWidth - 20;
		this.WeekChartAlto = this.WeekChart.nativeElement.offsetHeight;
	});
  }

  MesActual() : Mes
  {
  	return Meses.find(m => m.Valor == new Date().getMonth());
  }

  CargarTemporales()
  {
    var tmp = new Date();
    var dias = new Date(tmp.getFullYear(), tmp.getMonth() + 1, 0).getDate();
    for (var d = 1; d <= dias; d++) { this.DataByDays.push({valueX:0, valueY: d.toString(), minValueX:0, maxValueX: 0, selected: false}) }
  }

  CargarEstadisticas():void
  {
  	this.LoadingController.Mostrar();
    this.rest.StadisticsByDayOfWeek().subscribe((data : any) =>
    {

      this.Estadisticas = data.report.map(r => 
      {
      	let estadistica : estadisticas = 
      	{
      		Suma : r.suma,
      		Minimo : r.minimo,
      		Maximo : r.maximo,
      		Year : r.year,
      		Month : r.month,
      		Day : r.day,
          	WeekDay: r.weekDay,
      		Hour : r.hour
      	};

      	return estadistica;
      });

      this.Anos = this.Estadisticas.map(e => e.Year).filter((actual, index, acumulado) => acumulado.indexOf(actual) == index);
      this.Ano = this.Anos.find(a => a == new Date().getFullYear());

      if(this.Ano == null)
      {
      	this.Ano = new Date().getFullYear();
      	this.Anos.push(this.Ano);
      }

      this.CalcularDias(this.Mes.Number, this.Ano);
      this.GetEstaSemana();
      this.GetEsteAño(this.Ano);
      this.GetEsteMes(this.Ano, this.Mes.Number);
      this.EsteAnoElement.nativeElement.innerHTML = this.FormatNumber.nuevo(this.EsteAno, '$');
      this.EstaSemanaElement.nativeElement.innerHTML = this.FormatNumber.nuevo(this.EstaSemana, '$');
      this.EsteMesElement.nativeElement.innerHTML = this.FormatNumber.nuevo(this.EsteMes, '$');

      this.LoadingController.Cerrar(data.message);
    },
    (ex) =>
    {
      var mensaje = 
      {
        mostrar: true,
        mensaje: ex.error.message,
        status: 1
      };

      localStorage.setItem("mensaje",JSON.stringify(mensaje));
      this.LoadingController.Cerrar(ex.statusText);
    });

  }

  CambiarVista(valor: number):void
  {
  	var date = new Date();

  	switch (valor) 
  	{
  		case 0: this.CalcularMeses(this.Ano); break;
  		case 1: this.CalcularDias(this.Mes.Number, this.Ano); break;

  		default: break;
  	}
  }

  CambiarMes()
  {
  	switch (this.vista) 
  	{
  		case 0: this.CalcularMeses(this.Ano); break;
  		case 1: this.CalcularDias(this.Mes.Number, this.Ano); break;

  		default: break;
  	}

    var año = new Date().getFullYear();

    if(this.Mes != this.MesActual() || this.Ano != año)
    {
      this.EstaSemanaElement.nativeElement.style.opacity = '0.2';
      this.EstaSemanaElement.nativeElement.innerHTML = 'N/A';
    }
    else
    {
      this.EstaSemanaElement.nativeElement.style.opacity = '1';
      this.EstaSemanaElement.nativeElement.innerHTML = this.FormatNumber.nuevo(this.EstaSemana, '$');
    }

    this.GetEsteAño(this.Ano);
    this.EsteAnoElement.nativeElement.innerHTML = this.FormatNumber.nuevo(this.EsteAno, '$');

    this.GetEsteMes(this.Ano, this.Mes.Number);
    this.EsteMesElement.nativeElement.innerHTML = this.FormatNumber.nuevo(this.EsteMes, '$');
  }

  GetEstaSemana()
  {
    var date = new Date();
    var inicio = new Date(date.getFullYear(), date.getMonth(), date.getDate() - date.getDay());
    var fin = new Date(inicio.getFullYear(), inicio.getMonth(), inicio.getDate() +  6);

    inicio.setHours(0, 0, 0);
    fin.setHours(59, 59, 59);

    var tmp = this.Estadisticas.filter(e => this.GetFecha(e.Year, e.Month, e.Day) >= inicio && this.GetFecha(e.Year, e.Month, e.Day) <= fin);
    this.EstaSemana = (tmp.length > 0) ? tmp.map(e => e.Suma).reduce((acumulado, actual)=> actual + acumulado) : 0;
  }

  GetEsteAño(año : number)
  {
    var tmp = this.Estadisticas.filter(e => e.Year == año);
    this.EsteAno = (tmp.length > 0) ? tmp.map(e => e.Suma).reduce((acumulado,actual)=> acumulado + actual) : 0;
  }

  GetEsteMes(año : number, mes : number)
  {
    var tmp = this.Estadisticas.filter(e => e.Year == año && e.Month == mes);
    this.EsteMes = (tmp.length > 0) ? tmp.map(e => e.Suma).reduce((acumulado,actual)=> acumulado + actual) : 0;
  }

  GetFecha(año, mes, dia) : Date
  {
    return new Date(año, mes - 1, dia);
  }

  CalcularMeses(Año: number)
  {
    this.DataByDays = [];

    let mesesarray : Array<estadisticas> = this.SumaMeses().sort((a, b) => (a.Suma > b.Suma) ? -1 : (a.Suma < b.Suma) ? 1 : 0);

    let maxarray : Array<estadisticas> = mesesarray.slice(0, mesesarray.length / 3);
    let minarray : Array<estadisticas> = mesesarray.slice((mesesarray.length / 3 * 2), mesesarray.length);

    let maximo : number = maxarray.map(d => d.Suma).reduce((actual, acumulado)=> actual + acumulado) / maxarray.length;
    let minimo : number = minarray.map(d => d.Suma).reduce((actual, acumulado)=> actual + acumulado) / minarray.length;

    for (var m = 0; m < Meses.length; m ++) 
    {
    	let cantidad : number = 1;

    	var mes = Meses[m];

    	let Data : estadisticas = mesesarray.filter(d => d.Year == Año && d.Month == (m + 1))[0];
    	let suma : number = (Data != null) ? Data.Suma : 0;

    	var tmp = mesesarray.filter(d => d.Month == mes.Number);
    	var maxtmp = (tmp.length > 1) ? Math.max.apply(null, tmp.map(e => e.Suma)) : 0;
    	var mintmp = (tmp.length > 1) ? Math.min.apply(null, tmp.map(e => e.Suma)) : 0;
    	cantidad = (tmp.length > 1) ? cantidad + 1 : cantidad;

    	this.DataByDays.push(
		{
			valueX: suma,
			valueY: mes.Nombre,

			minValueX: (minimo + mintmp) / cantidad,
			maxValueX: (maximo + maxtmp) / cantidad,

			selected:false
		})
    }
  }

  SumaMeses() : Array<estadisticas>
  {
  	let res : Array<estadisticas> = [];

  	let años : Array<number> = this.Estadisticas.map(e => e.Year).filter((actual, index, acumulado)=> acumulado.indexOf(actual) == index);
  	for(var a = 0; a < años.length ; a++)
  	{
  		let año : number = años[a];
  		let meses : Array<number> = this.Estadisticas.filter(e => e.Year == año).map(e => e.Month).filter((actual, index, acumulado) => acumulado.indexOf(actual) == index);

  		for(var m = 0; m < meses.length; m++)
  		{
  			let mes : number = meses[m];
  			let suma : number = this.Estadisticas.filter(e => e.Year == año && e.Month == mes).map(e => e.Suma).reduce((a, ac)=> a + ac);

  			res.push(
  			{
  				Suma : suma,
  				Minimo : 0,
  				Maximo : 0,
  				Year : año,
  				Month : mes,
  				Day : null,
  				WeekDay : null,
  				Hour : null
  			});
  		}
  	}

  	return res; 
  }

  SumaDias() : Array<estadisticas>
  {
  	let res : Array<estadisticas> = [];

  	let años : Array<number> = this.Estadisticas.map(e => e.Year).filter((actual, index, acumulado)=> acumulado.indexOf(actual) == index);
  	for(var a = 0; a < años.length ; a++)
  	{
  		let año : number = años[a];
  		let meses : Array<number> = this.Estadisticas.filter(e => e.Year == año).map(e => e.Month).filter((actual, index, acumulado) => acumulado.indexOf(actual) == index);

  		for(var m = 0; m < meses.length; m++)
  		{
  			let mes : number = meses[m];
  			let dias : Array<number> = this.Estadisticas.filter(e =>e.Year == año && e.Month == mes).map(e=> e.Day).filter((actual, index, acumulado) => acumulado.indexOf(actual) == index);

  			for(var d = 0; d < dias.length ; d++)
  			{
  				let dia : number = dias[d];
	  			let suma : number = this.Estadisticas.filter(e => e.Year == año && e.Month == mes && e.Day == dia).map(e => e.Suma).reduce((actual, acumulado)=> actual + acumulado);

	  			res.push(
	  			{
	  				Suma : suma,
	  				Minimo : 0,
	  				Maximo : 0,
	  				Year : año,
	  				Month : mes,
	  				Day : dia,
	  				WeekDay : this.Estadisticas.find(e => e.Year == año && e.Month == mes && e.Day == dia).WeekDay,
	  				Hour : null
	  			});
	  		}
  		}
  	}

  	return res; 
  }

  CalcularDias(Mes: number, Año: number)
  {
    this.DataByDays = [];

    let diasarray : Array<estadisticas> = this.SumaDias().sort((a, b) => (a.Suma > b.Suma) ? -1 : (a.Suma < b.Suma) ? 1 : 0);

  	let fraccion : number = 6;
    let maxarray : Array<estadisticas> = diasarray.slice(0, diasarray.length / fraccion);
    let minarray : Array<estadisticas> = diasarray.slice((diasarray.length / fraccion) * (fraccion - 1), diasarray.length);

    let maximo : number = maxarray.map(d => d.Suma).reduce((actual, acumulado)=> actual + acumulado) / maxarray.length;
    let minimo : number = minarray.map(d => d.Suma).reduce((actual, acumulado)=> actual + acumulado) / minarray.length;

    for (var dia = 1; dia <= new Date(Año, Mes, 0).getDate(); dia++) 
    {
    	let cantidad : number = 1;

    	let Data : estadisticas = diasarray.filter(d => d.Year == Año && d.Month == Mes && d.Day == dia)[0];
  		let suma : number = (Data != null) ? Data.Suma : 0;

  		var tmp = diasarray.filter(d => d.Day == dia);
    	let maxarraytmp : Array<estadisticas> = tmp.slice(0, tmp.length / fraccion);
    	let minarraytmp : Array<estadisticas> = tmp.slice((tmp.length / fraccion) * (fraccion - 1), tmp.length);
    	let maxtmp : number = (maxarraytmp.length > 0) ? maxarraytmp.map(d => d.Suma).reduce((actual, acumulado)=> actual + acumulado) / maxarraytmp.length : 0;
    	let mintmp : number = (maxarraytmp.length > 0) ? minarraytmp.map(d => d.Suma).reduce((actual, acumulado)=> actual + acumulado) / minarraytmp.length : 0;
    	cantidad = (tmp.length > 1) ? cantidad + 1 : cantidad;

    	var tmp2 = diasarray.filter(d => d.Day == dia && d.Month == Mes);
    	let maxarraytmp2 : Array<estadisticas> = tmp2.slice(0, tmp2.length / fraccion);
    	let minarraytmp2 : Array<estadisticas> = tmp2.slice((tmp2.length / fraccion) * (fraccion - 1), tmp2.length);
    	let maxtmp2 : number = (maxarraytmp2.length > 0) ? maxarraytmp2.map(d => d.Suma).reduce((actual, acumulado)=> actual + acumulado) / maxarraytmp2.length : 0;
    	let mintmp2 : number = (maxarraytmp2.length > 0) ?  minarraytmp2.map(d => d.Suma).reduce((actual, acumulado)=> actual + acumulado) / minarraytmp2.length : 0;
    	cantidad = (tmp2.length > 1) ? cantidad + 1 : cantidad;

  		this.DataByDays.push(
    	{
        valueX: suma,
        valueY: dia.toString(),

        minValueX: (minimo + mintmp + mintmp2) / cantidad,
		    maxValueX: (maximo + maxtmp + mintmp2) / cantidad,

        selected:false
    	})
    }

    var date = new Date();
    if(this.Ano == date.getFullYear() && this.Mes.Valor == date.getMonth())
    {
      this.Data = this.NombreDias.map(d => {
        var inicio = new Date(date.getFullYear(), date.getMonth(), date.getDate() - date.getDay());
        var fin = new Date(inicio.getFullYear(), inicio.getMonth(), inicio.getDate() +  6);
        var arrdia = diasarray.filter(e => this.GetFecha(e.Year, e.Month, e.Day) >= inicio && this.GetFecha(e.Year, e.Month, e.Day) <= fin && e.WeekDay == d.valor + 1 && e.Month == this.Mes.Number && e.Year == this.Ano ).map(e => e.Suma);

        var arrweekday = diasarray.filter(e => e.WeekDay == d.valor + 1);

        let maxarray : Array<estadisticas> = arrweekday.slice(0, arrweekday.length / fraccion);
        let minarray : Array<estadisticas> = arrweekday.slice((arrweekday.length / fraccion) * (fraccion - 1), arrweekday.length);

        let maximo : number = maxarray.map(d => d.Suma).reduce((actual, acumulado)=> actual + acumulado) / maxarray.length;
        let minimo : number = minarray.map(d => d.Suma).reduce((actual, acumulado)=> actual + acumulado) / minarray.length;

        let res : Data = 
        {
          valueX : (arrdia.length > 0) ? arrdia.reduce((acumulado, actual)  => acumulado + actual) : 0,
          valueY : d.nombre,
          maxValueX : maximo,
          minValueX : minimo,
          selected : false
        }

        return res;
      });
    }
  }

}
