import { Component, OnInit, Input, ViewChild} from '@angular/core';
import { Unidades } from '../Enum/Unidades';
import { MatTableDataSource, MatSort, MatPaginator } from '@angular/material'




@Component({
  selector: 'app-cds-table',
  templateUrl: './cds-table.component.html',
  styleUrls: ['./cds-table.component.css']
})

export class CdsTableComponent implements OnInit {
	@Input() Items : any;
	@ViewChild(MatPaginator, {static: true}) paginator: MatPaginator;

	filtro;
	dataSource = new MatTableDataSource();

	nombres : string[];

	getValue(item : any, key : string):string
	{
		var value = item[key];
		return (value == undefined) ? '?' : value;
		//return JSON.stringify(item);
	}

	constructor() 
	{

	}
	ngOnInit() 
	{
		if(this.Items != null)
	  	{
	  		if(this.Items.length > 0)
	  		this.nombres = Object.keys(this.Items[0]);
	  		this.dataSource.data = this.Items.slice();
	  		//this.dataSource.sort = this.sort;
	  		this.dataSource.paginator=this.paginator;
	  	}
	  	else
	  	{
	  		console.error("No se definio 'Items' para cds-table");
	  	}
	}

	cargar():void
	{
		if(this.Items.length > 0)
	  		this.nombres = Object.keys(this.Items[0]);
	  		this.dataSource.data = this.Items.slice();
	  		//this.dataSource.sort = this.sort;
	  		this.dataSource.paginator=this.paginator;
	}

	applyFilter(filterValue: string) 
	{
    	filterValue = filterValue.trim(); // Remove whitespace
    	filterValue = filterValue.toLowerCase(); // Datasource defaults to lowercase matches
    	this.dataSource.filter = filterValue;
	}

}
