import { MDevice } from './Models/Device';
import { OperativeSystem } from './Models/OperativeSystem';
import { Browser } from './Models/Browser';
import { SystemInformation } from './Enum/SystemInformation';


export class SystemInfo
{
	header = [navigator.platform, navigator.userAgent, navigator.appVersion, navigator.vendor];
	dataos: OperativeSystem[]=
	[
	    { Name: 'Windows Phone', Value: 'Windows Phone', Version: 'OS' },
	    { Name: 'Windows', Value: 'Win', Version: 'NT' },
	    { Name: 'iPhone', Value: 'iPhone', Version: 'OS' },
	    { Name: 'iPad', Value: 'iPad', Version: 'OS' },
	    { Name: 'Kindle', Value: 'Silk', Version: 'Silk' },
	    { Name: 'Android', Value: 'Android', Version: 'Android' },
	    { Name: 'PlayBook', Value: 'PlayBook', Version: 'OS' },
	    { Name: 'BlackBerry', Value: 'BlackBerry', Version: '/' },
	    { Name: 'Macintosh', Value: 'Mac', Version: 'OS X' },
	    { Name: 'Linux', Value: 'Linux', Version: 'rv' },
	    { Name: 'Palm', Value: 'Palm', Version: 'PalmOS' }
	]

	databrowser: Browser[]=
	[
		{ Name: 'Microsoft Edge', Value: 'Edge'},
        { Name: 'Firefox', Value: 'Firefox'},
        { Name: 'Google Chrome', Value: 'Chrome'},
        { Name: 'Safari', Value: 'Safari'},
        { Name: 'Internet Explorer', Value: 'MSIE'},
        { Name: 'Opera', Value: 'Opera'},
        { Name: 'BlackBerry', Value: 'CLDC'},
        { Name: 'Mozilla', Value: 'Mozilla'}
    ]

	getDevice():MDevice
	{
		var info = this.init();
		var device = 
		{
			OS: info.os,
			Browser: info.browser
		}

		return device;
	}

	getOS (string) : OperativeSystem
	{
        var html = '',
            regex,
            regexv,
            match,
            matches,
            Version;

        var SistemasOperativos : OperativeSystem[] = this.dataos;
        
        for (var so = 0; so < SistemasOperativos.length; so += 1) 
        {
        	var SistemaOperativo : OperativeSystem = SistemasOperativos[so]; 
            regex = new RegExp(SistemaOperativo.Value, 'i');
            match = regex.test(string);
            if (match)
            {
                regexv = new RegExp(SistemaOperativo.Version + '[- /:;]([\\d._]+)', 'i');
                matches = string.match(regexv);
                Version = '';

                if (matches) 
            	{ 
            		if (matches[1]) 
        			{ 
        				matches = matches[1]; 
        			} 
        		}

                if (matches) {
                    matches = matches.split(/[._]+/);
                    for (var j = 0; j < matches.length; j += 1) {
                        if (j === 0) {
                            Version += matches[j] + '.';
                        } else {
                           Version += matches[j];
                        }
                    }
                } else {
                    Version = '0';
                }
                return {
                    Name: SistemaOperativo.Name,
                    Value: SistemaOperativo.Value,
                    Version: SistemaOperativo.Version + " "+ parseFloat(Version)
                };
            }
        }
        return { Name: 'unknown', Value: 'unknow', Version: "0" };
    }

    getBrowser (string) : Browser
	{
        var html = '',
            regex,
            regexv,
            match,
            matches,
            Version;

        var Exploradores : Browser[] = this.databrowser;

        for (var e = 0; e < Exploradores.length; e += 1) {
        	var explorador : Browser = Exploradores[e];

            regex = new RegExp(explorador.Value, 'i');
            match = regex.test(string);
            if (match)
            {
                matches = string.match(regexv);
                Version = '';

                if (matches) 
            	{ 
            		if (matches[1]) 
        			{ 
        				matches = matches[1]; 
        			} 
        		}

                return {
                    Name: explorador.Name,
                    Value: explorador.Value
                };
            }
        }
        return { Name: 'unknown', Value: 'unknow'};
    }


	init () 
	{
		var agent = this.header.join(' '),
		os = this.getOS(agent),
		browser = this.getBrowser(agent);

		return { os: os, browser: browser }; 
	}
}