import { Component, OnInit, Inject } from '@angular/core';
import {MAT_DIALOG_DATA, MatDialogRef} from "@angular/material";

import { MessageInformation } from '../../../Models/MessageInformation';
import { MessageTypes } from '../../../Enum/MessageTypes';

export enum Color
{
	Verder = 0,
	Rojo = 1
}

export interface YesNoDialogData
{
  Titulo : string;
  Mensaje : string;
  Accion : string;
  Color : Color;
  Information : Array<MessageInformation>;
}


@Component({
  selector: 'app-yes-no-dialog',
  templateUrl: './yes-no-dialog.component.html',
  styleUrls: ['./yes-no-dialog.component.css', '../../../Tema.css']
})

export class YesNoDialogComponent implements OnInit {

  constructor(public dialogRef: MatDialogRef<YesNoDialogComponent>, @Inject(MAT_DIALOG_DATA) public data: YesNoDialogData) { }

  ngOnInit() {
  	console.log(this.data.Information)
  }

  Aceptar()
  {
  	this.dialogRef.close(true);
  }

  Close(): void
  {
  	this.dialogRef.close(false);
  }

}
