  Renovar(pension : Pension)
  {
    let data : RenewModalData =
    {
      Pension : pension,
      Rates : null
    };

    this.LoadingController.Mostrar();
    this.rest.getPensionPrice().subscribe((result : any)=>
    {
      this.LoadingController.Cerrar(result.message);
      if(result.success)
      {
        let Rates : Array<Rate> = result.report.map(r=>
        {
          let rate : Rate = 
          {
            Id: r.id,
            Nombre: r.nombre,
            Price: r.precio,
            Unit: r.unidad,
            Quantity: r.cantidad
          }
          return rate;
        });

        data.Rates = Rates;

        const dialogRef = this.dialog.open(RenewModalComponent,
        {
          width: '600px',
          disableClose: true,
          data : data
        });

        dialogRef.afterClosed().subscribe((data)=>
        {
          if(data != null)
          {
            this.LoadingController.Mostrar();
            this.rest.GetIdCode(data.Pension.Code).subscribe((result : any)=>
            {
              var x = 
              {
                IdPension : data.Pension.Id,
                NewEndDate : data.NewDate,
                Terminal : 18,
                IdRate : data.Rate.Id,
                Tiempo : data.Tiempo,
                Unidad : data.Rate.Unit,
                Cantidad : data.Cantidad,
                IdCode : result.Id,
                Tag : data.Pago
              }
              console.log(x);
              this.rest.Renovar(x).subscribe((result : any)=>
              {
                this.LoadingController.Cerrar(result.message);
                if(result.success)
                {
                  pension.Vencimiento = data.NewDate;
                  this.CargarPensiones();
                  this.MessageController.Mostrar(result.message, MessageType.Ok);
                }
                else
                {
                  this.MessageController.Mostrar(result.message, MessageType.Error);
                }
              },
              (ex) =>
              {
                this.LoadingController.Cerrar(ex.statusText);
                this.MessageController.Mostrar(ex.error.message, MessageType.Error);
              });
            },
            (ex) =>
            {
              this.LoadingController.Cerrar(ex.statusText);
              this.MessageController.Mostrar(ex.error.message, MessageType.Error);
            });
          }
        },
        (ex) =>
        {
          this.LoadingController.Cerrar(ex.statusText);
          this.MessageController.Mostrar(ex.error.message, MessageType.Error);
        });
      }
      else
      {
        this.MessageController.Mostrar(result.message, MessageType.Error);
      }
    },
    (ex)=>
    {
      this.LoadingController.Cerrar(ex.statusText);
      this.MessageController.Mostrar(ex.error.message, MessageType.Error);
    });
  }

  Suspender(pension : Pension)
  {
    let data : YesNoDialogData =
    {
      Titulo : "¿Suspender Pensión?",
      Mensaje : "La pensión de " + pension.Nombre + " (" + pension.Code + ") será suspendida.",
      Accion : "Suspender",
      Color : Color.Rojo,

      Information : 
      [
        {
          Message : "Esta pensión ya no podra hacer uso del estacionamiento.\nSe puede reactivar después.",
          Type : MessageTypes.Information
        }
      ]
    };

    if(pension.Estatus == EstatusPension.Activo)
    {
      data.Information.push(
      {
        Message : "Esta pensión se encuentra vigente.",
        Type : MessageTypes.Warning
      });
    }

    const dialogRef = this.dialog.open(YesNoDialogComponent, 
    {
      width: '550px',
      disableClose: false,
      data: data
    });

    dialogRef.afterClosed().subscribe(result => 
    {
      if(result == true)
      {
        this.LoadingController.Mostrar();
        //Se tiene asignada un Id Botón
        if(pension.IdBoton != null)
        {
          this.rest.getIdPersonByPension(pension.Id).subscribe((result : any)=>
          {
            if(result.success)
            {
              let IdPerson : number = result.IdPerson; 
              this.rest.BotonDeletePension(IdPerson).subscribe((result : any)=>
              {
                //la peticion se completo con éxito
                if(result.success)
                {
                  this.rest.SuspendPension(pension.Id, new Date()).subscribe((result : any)=>
                  {
                    pension.Estatus = EstatusPension.Suspendido;
                    this.CargarFiltros();
                    this.LoadingController.Cerrar(result.message);
                  },
                  (ex) =>
                  {
                    this.LoadingController.Cerrar(ex.statusText);
                    this.MessageController.Mostrar(ex.message, MessageType.Error);
                  })
                }
                else
                {
                  this.LoadingController.Cerrar(result.message);
                  this.MessageController.Mostrar(result.message, MessageType.Error);
                }
              },
              (ex)=>
              {
                //todo salio mal
                this.LoadingController.Cerrar(ex.statusText);
                this.MessageController.Mostrar(ex.message, MessageType.Error);
              });
            }
            else
            {
              this.LoadingController.Cerrar(result.message);
              this.MessageController.Mostrar(result.message, MessageType.Error);
            }
          },
          (ex)=>
          {
            //todo salio mal
            this.LoadingController.Cerrar(ex.statusText);
            this.MessageController.Mostrar(ex.message, MessageType.Error);
          });
        }
        // No se tiene asignado un Id Botón
        else
        {
          this.rest.SuspendPension(pension.Id, new Date()).subscribe((result : any)=>
          {
            pension.Estatus = EstatusPension.Suspendido;
            this.CargarFiltros();
            this.LoadingController.Cerrar(result.message);
          },
          (ex) =>
          {
            this.LoadingController.Cerrar(ex.statusText);
            this.MessageController.Mostrar(ex.message, MessageType.Error);
          })
        }
      }
    });
  }

