import { Component, OnInit, ViewChild, Output, EventEmitter} from '@angular/core';
import { RestService } from '../../rest.service';
import { Observable } from 'rxjs';
import { MatTableDataSource, MatSort, MatPaginator } from '@angular/material';
import { CodeTypes } from '../../Enum/CodeTypes';
import { EventTypes } from '../../Enum/EventTypes';
import { FormatNumber } from '../../Funciones';
import {SelectionModel} from '@angular/cdk/collections';
import {FlatTreeControl} from '@angular/cdk/tree';
import {Injectable} from '@angular/core';
import {MatTreeFlatDataSource, MatTreeFlattener} from '@angular/material/tree';
import {BehaviorSubject} from 'rxjs';
import { Meses } from '../../Enum/Meses';

interface Fecha
{
  año: number,
  mes: number,
  dia: number,
  fecha: Date
}

interface Nodo {
  expandible: boolean;
  nombre: string;
  nivel: number;
  fecha: Date;
}

interface NodoFecha {
  nombre: string;
  hijos?: NodoFecha[];
  fecha?: Date;
}

@Component({
  selector: 'app-ocupaciones',
  templateUrl: './ocupaciones.component.html',
  styleUrls: ['./ocupaciones.component.css']
})
export class OcupacionesComponent implements OnInit 
{
	@Output() ChangeOcupaciones = new EventEmitter();

	@ViewChild(MatPaginator, {static: true}) paginator: MatPaginator;
	@ViewChild(MatSort, {static: true}) sort: MatSort;

  tableFilters = [];

  filter = 
  {
    code: false,
    codetype: false,
    eventtype: false,
    eventDate: false
  }

  filterTipoCodigo =
  [
    {nombre:"opcion1", selected:false, value: 0},
    {nombre:"opcion2", selected:true, value: 1}
  ];

  filterTipoEvento =
  [
    {nombre:"opcion1", selected:false, value: 0},
    {nombre:"opcion2", selected:true, value: 1}
  ];

  filterCodeValue = "";

	FormatNumber = new FormatNumber();

	Totales = [];

  private NodoByNodoFecha = (nodo: NodoFecha, nivel: number) => {
    return {
      expandible: !!nodo.hijos && nodo.hijos.length > 0,
      nombre: nodo.nombre,
      nivel: nivel,
      fecha: nodo.fecha
    };
  }

  getLevel = (node: Nodo) => node.nivel;
  TieneHijos = (_: number, nodo: Nodo) => nodo.expandible;
  treeControl = new FlatTreeControl<Nodo>(nodo => nodo.nivel, nodo => nodo.expandible);
  treeFlattener = new MatTreeFlattener(this.NodoByNodoFecha, nodo => nodo.nivel, nodo => nodo.expandible, nodo => nodo.hijos);
  arbol = new MatTreeFlatDataSource(this.treeControl, this.treeFlattener);
  checklistSelection = new SelectionModel<Nodo>(true);

	displayedColumns: string[] = ['code.value', 'code.type', 'event.eventType', 'event.date'];
	dataSource;

  countVisibles():number
  {
    var v = 0;
    var w = this.treeControl.dataNodes.filter( d => d.nivel == 0).length;
    var x = this.treeControl.dataNodes.filter( d => this.treeControl.isExpanded(d));
    var y = x.map( n => this.countHijos(n));
    if ( y.length > 0) var z = y.reduce(this.reducer);
    z = (z) ? z : 0;
    var res = (48 * (w + z)) + 40;
    return (res > 500) ? 500 : res;
  }

  reducer = (accumulator, currentValue) => accumulator + currentValue;

  countHijos(nodo:Nodo):number
  {
    var y = this.treeControl.getDescendants(nodo);
    y = y.filter(n => n.nivel == nodo.nivel + 1);
    return y.length;
  }

  hijosSeleccionados(node: Nodo): boolean {
    const descendants = this.treeControl.getDescendants(node);
    const descAllSelected = descendants.every(child =>
      this.checklistSelection.isSelected(child)
    );
    return descAllSelected;
  }

  todoItemSelectionToggle(node: Nodo): void {
    this.checklistSelection.toggle(node);
    const descendants = this.treeControl.getDescendants(node);
    this.checklistSelection.isSelected(node)
      ? this.checklistSelection.select(...descendants)
      : this.checklistSelection.deselect(...descendants);

    // Force update for the parent
    descendants.every(child =>
      this.checklistSelection.isSelected(child)
    );
    this.checkAllParentsSelection(node);
    this.filterEventDate();
  }

  checkAllParentsSelection(node: Nodo): void {
    let parent: Nodo | null = this.getParentNode(node);
    while (parent) {
      this.checkRootNodeSelection(parent);
      parent = this.getParentNode(parent);
    }
  }

  checkRootNodeSelection(node: Nodo): void {
    const nodeSelected = this.checklistSelection.isSelected(node);
    const descendants = this.treeControl.getDescendants(node);
    const descAllSelected = descendants.every(child =>
      this.checklistSelection.isSelected(child)
    );
    if (nodeSelected && !descAllSelected) {
      this.checklistSelection.deselect(node);
    } else if (!nodeSelected && descAllSelected) {
      this.checklistSelection.select(node);
    }
  }

  getParentNode(node: Nodo): Nodo | null {
    const currentLevel = this.getLevel(node);

    if (currentLevel < 1) {
      return null;
    }

    const startIndex = this.treeControl.dataNodes.indexOf(node) - 1;

    for (let i = startIndex; i >= 0; i--) {
      const currentNode = this.treeControl.dataNodes[i];

      if (this.getLevel(currentNode) < currentLevel) {
        return currentNode;
      }
    }
    return null;
  }

  descendantsPartiallySelected(node: Nodo): boolean {
    const descendants = this.treeControl.getDescendants(node);
    const result = descendants.some(child => this.checklistSelection.isSelected(child));
    return result && !this.hijosSeleccionados(node);
  }

  isSelected(nodo: Nodo):boolean
  {
    return this.checklistSelection.isSelected(nodo);
  }

  SeleccionarNodo(nodo: Nodo)
  {
    this.checklistSelection.toggle(nodo);
    this.checkAllParentsSelection(nodo);
    this.filterEventDate();
  }

	constructor(private rest : RestService) { }

	ngOnInit() 
	{
		this.CargarReporte(); 
	}

  filterEventDate()
  {
    var filtro = this.tableFilters.filter(tf => tf.id == 'event.date')[0];
    var f = this.checklistSelection.selected.filter(n => n.nivel == 2).map( n => n.fecha);

    if(filtro == null || filtro == undefined)
    {
      this.tableFilters.push(
      {
        id: 'event.date',
        values: f
      });
    }

    filtro = this.tableFilters.filter(tf => tf.id == 'event.date')[0];
    filtro.values = f;

    if(this.checklistSelection.selected.length == this.treeControl.dataNodes.length)
    {
      this.tableFilters = this.tableFilters.filter(f => f.id != 'event.date');
    }

    this.dataSource.filter = JSON.stringify(this.tableFilters);
    if (this.dataSource.paginator) {
      this.dataSource.paginator.firstPage();
    }

  }

  filterET(e)
  {
    var filtro = this.tableFilters.filter(tf => tf.id == 'event.eventType')[0];
    var f = this.filterTipoEvento.filter(te => te.selected == true);

    if(filtro == null || filtro == undefined)
    {
      this.tableFilters.push(
      {
        id: 'event.eventType',
        values: f
      });
    }

    filtro = this.tableFilters.filter(tf => tf.id == 'event.eventType')[0];
    filtro.values = f;

    if(this.filterTipoEvento.length == f.length)
    {
      this.tableFilters = this.tableFilters.filter(f => f.id != 'event.eventType');
    }

    this.dataSource.filter = JSON.stringify(this.tableFilters);
    if (this.dataSource.paginator) {
      this.dataSource.paginator.firstPage();
    }
  }

  filterTC(e)
  {
    var filtro = this.tableFilters.filter(tf => tf.id == 'code.type')[0];
    var f = this.filterTipoCodigo.filter(te => te.selected == true);

    if(filtro == null || filtro == undefined)
    {
      this.tableFilters.push(
      {
        id: 'code.type',
        values: f
      });
    }

    filtro = this.tableFilters.filter(tf => tf.id == 'code.type')[0];
    filtro.values = f;

    if(this.filterTipoCodigo.length == f.length)
    {
      this.tableFilters = this.tableFilters.filter(f => f.id != 'code.type');
    }

    this.dataSource.filter = JSON.stringify(this.tableFilters);
    if (this.dataSource.paginator) {
      this.dataSource.paginator.firstPage();
    }
  }

  filterCode()
  {
    var filtro = this.tableFilters.filter(tf => tf.id == 'code.value')[0];
    if(filtro == null || filtro == undefined)
    {
      this.tableFilters.push(
      {
        id: 'code.value',
        value: this.filterCodeValue
      });
    }

    filtro = this.tableFilters.filter(tf => tf.id == 'code.value')[0];
    filtro.value = this.filterCodeValue;

    if(this.filterCodeValue == "")
    {
      this.tableFilters = this.tableFilters.filter(f => f.id != 'code.value');
    }

    this.dataSource.filter = JSON.stringify(this.tableFilters);
    if (this.dataSource.paginator) {
      this.dataSource.paginator.firstPage();
    }
  }

  checkFiltro(filtro:string):string
  {
    var f = this.tableFilters.filter(f => f.id == filtro)[0];
    return (f!=null && f!=undefined)?'1':'.2';
  }

  showFilter(Filtro:string):void
  {
    switch (Filtro) {
      case "Code":
        this.filter.code = !this.filter.code;
        this.filter.codetype = false;
        this.filter.eventtype = false;
        this.filter.eventDate = false;
        break;
      case "TipoCodigo":
        this.filter.code = false;
        this.filter.codetype = !this.filter.codetype;
        this.filter.eventtype = false;
        this.filter.eventDate = false;
        break;
      case "TipoEvento":
        this.filter.code = false;
        this.filter.codetype = false;
        this.filter.eventtype = !this.filter.eventtype;
        this.filter.eventDate = false;
        break;
      case "FechaEvento":
        this.filter.code = false;
        this.filter.codetype = false;
        this.filter.eventtype = false;
        this.filter.eventDate = !this.filter.eventDate;
        break;
      default:
        // code...
        break;
    }
  }

	Decimal(hexadecimal: string) : number
	{
		return	parseInt(hexadecimal, 16);
	}

	getEventType(et): string
	{
		return EventTypes[et];
	}

  getEventStatus(status):string
  {
    return 'led big ' + ((status == '1')?'gray':'red');
  }

  getCodeType(ct): string
  {
    return CodeTypes[ct];
  }

	getPropertyByPath(obj: Object, pathString: string)
	{
  	return pathString.split('.').reduce((o, i) => o[i], obj);
	}

  getShortDate(date):string
  {
    return new Date(date).toLocaleString("es-ES", 
    { 
      year: '2-digit', 
      month: 'short', 
      day: '2-digit', 
      hour: '2-digit',  
      minute: '2-digit',
      hour12: true 
    }); 
  }

  getLongDate(date):string
  {
    return new Date(date).toLocaleString("es-ES", 
    { 
      year: 'numeric', 
      month: 'long', 
      day: 'numeric', 
      hour: '2-digit',  
      minute: '2-digit',
      second: '2-digit',
      weekday: 'long' 
    }); 
  }

	CalcularTotales():void
	{
    	var tipos = this.dataSource.data.map(d => d.code.type).filter((v, i, a) => a.indexOf(v) === i);
    	var total = this.dataSource.data.length;

    	for (var t = 0; t < tipos.length; t++) 
    	{
      	var tmp = this.dataSource.data.filter(d => d.code.type == tipos[t]);
      	var valor = tmp.length;
      	var p = (100/total * valor).toFixed(2);

      	if(this.Totales.length != tipos.length)
      	{
          	this.Totales.push(
          	{
            	Titulo: CodeTypes[tipos[t]] + " "+ this.FormatNumber.nuevo(p, '') +"%",
            	Porcentaje: p,
            	Valor: this.FormatNumber.nuevo(valor, ''),
            	tipo: tipos[t]
          	});

        		//se ordenan los totales respecto al porcentaje de mayor a menor.
        		this.Totales = this.Totales.sort((a, b) => (a.Porcentaje < b.Porcentaje) ? 1 : -1)
      	}
      	else
      	{
        		var c = this.Totales.filter(c => c.tipo == tipos[t])[0];
        
			c.Titulo = CodeTypes[tipos[t]] + " "+ this.FormatNumber.nuevo(p, '') +"%";
			c.Porcentaje = p;
			c.Valor = this.FormatNumber.nuevo(valor, '');
      	}
    	}

	}

	CargarReporte()
	{
    localStorage.setItem("cargando","true");
  	this.Totales = [];
    // se obtiene el reporte de ocupaciones.
    this.rest.getReporteOcupaciones().subscribe((data : any) =>
    {
    	//guarda la respuesta recibida del WS.
		  var respuesta = data;

    	//console.log(respuesta.report);

    	//se guarda el reporte y se asigna el ordenamiento y la paginación.
    	this.dataSource = new MatTableDataSource(data.report.report);
		  this.dataSource.sort = this.sort;
    	this.dataSource.paginator = this.paginator;
    	this.dataSource.sortingDataAccessor = (data, sortHeaderId: string) => 
    	{
    		return this.getPropertyByPath(data, sortHeaderId);
  		};


  		this.CalcularTotales();
      this.cargarFiltros();

  		this.ChangeOcupaciones.emit(
			{
				parkingSpots: respuesta.report.parkingSpots,
				Total: this.dataSource.data.length 
			});
    		//this.cargarFiltros();

      this.dataSource.filterPredicate = 
      (data, filtersJson: string) => 
      {
        var match = true;
        const matchFilter = [];
        const filters = JSON.parse(filtersJson);

        filters.forEach(filter => 
        {
          var val;
          var datos = filter.id.split('.');
          val = data[datos[0]] === null ? '' : data[datos[0]];

          for (var d = 1; d < datos.length; d++) 
          {
            val = val[datos[d]] === null ? '' : val[datos[d]];
          }

          switch (filter.id) 
          {
            case "code.value":
              match = match && (val.toString().toLowerCase().includes(filter.value.toLowerCase())); 
              //console.log(val.toLocaleString() +" >= "+ this.dateRange[0].toLocaleString() +" && " + val.toLocaleString() + " <= " + filter.value.toLocaleString() + " = " + (val >= this.dateRange[0] && val <= filter.value));
            break;
            case "code.type":
              var f = filter.values.map(o => o.value);
              //console.log(val);
              match = match && (f.includes(val));
            break;
            case "event.eventType":
              var f = filter.values.map(o => o.value);
              //console.log(val);
              match = match && (f.includes(val));
            break;
            case "event.date":
              var f = filter.values.map(f => new Date(f)).map(f => { return { año: f.getFullYear(), mes: f.getMonth(), dia: f.getDate()}});
              var fecha = new Date(val);
              var s = { año: fecha.getFullYear(), mes: fecha.getMonth(), dia: fecha.getDate()};
              var x = f.filter(f => f.año == s.año && f.mes == s.mes && f.dia == s.dia)[0];
              match = match && ( !(x == null || x== undefined));
            break;
            
            default:
            console.log(filter.id);
              match = match && val.toString().toLowerCase().includes(filter.value.toLowerCase());
            break;
          }
          matchFilter.push(match);
        });
        return matchFilter.every(Boolean);
      };
      localStorage.setItem("cargando","false");
    },
    (ex) =>
    {
      var mensaje = 
      {
        mostrar: true,
        mensaje: ex.error.message,
        status: 1
      };
      localStorage.setItem("mensaje",JSON.stringify(mensaje));
      localStorage.setItem("cargando","false");
    });
  }

  cargarFiltros()
  {
    // -  -  -  -  -  -  Tipo Código -  -  -  -  -  -
    this.filterTipoCodigo = this.dataSource.data.map(d => d.code.type).filter((v, i, a) => a.indexOf(v) === i).map(o => { return {nombre: CodeTypes[o], value: o, selected:true}});

     // -  -  -  -  -  -  Tipo Evento -  -  -  -  -  -
    this.filterTipoEvento = this.dataSource.data.map(d => d.event.eventType).filter((v, i, a) => a.indexOf(v) === i).map(o => { return {nombre: EventTypes[o], value: o, selected:true}});

    // - - - - - - - - - Fecha - - - - - - - - - - - 
    var fechas = this.dataSource.data.map(d => new Date(d.event.date)).map(f => { return { año: f.getFullYear(), mes: f.getMonth(), dia: f.getDate(), fecha: f}});
    fechas = fechas.sort((a, b) => (a.dia < b.dia) ? -1 : 1);
    fechas = fechas.sort((a, b) => (a.mes < b.mes) ? -1 : 1);
    fechas = fechas.sort((a, b) => (a.año < b.año) ? -1 : 1);

    var datos : NodoFecha[] = [];

    for (var f =  0; f < fechas.length; f++) 
    {
      var fecha: Fecha = fechas[f];

      var nodoy = datos.filter(n => n.nombre == fecha.año.toString())[0]; 
      if(nodoy == null)
      {
        datos.push(
        {
          nombre: fecha.año.toString(),
          hijos: []
        });

        nodoy = datos[datos.length - 1]; 
      }

      var nodom = nodoy.hijos.filter(n => n.nombre == Meses[fecha.mes])[0];
      if(nodom == null)
      {
        nodoy.hijos.push(
        {
          nombre: Meses[fecha.mes],
          hijos: []
        });

        nodom = nodoy.hijos[nodoy.hijos.length - 1];
      }

      var nodod = nodom.hijos.filter(n => n.nombre == fecha.dia.toString())[0];
      if(nodod == null)
      {
        nodom.hijos.push(
        {
          nombre: fecha.dia.toString(),
          fecha: fecha.fecha
        })
      }
    }

    this.arbol.data = datos;


    // - - - - - - - - - Limpiar Filtros - - - -  - -
    this.tableFilters = [];
    this.filterCodeValue = "";
    if (this.dataSource.paginator) {
      this.dataSource.paginator.firstPage();
    }
  }
}