import { Component, OnInit, Inject } from '@angular/core';
import { Modulo, Permiso } from '../Models/Modulo';
import { User } from '../Models/User';
import { MAT_DIALOG_DATA, MatDialogRef} from "@angular/material";
import { Group } from '../Models/Group';
import { RestService } from '../rest.service';
import { MessageController } from '../Controllers/MessageController';
import { MessageType } from '../Views/Dialog/message/message.component';
import { LoadingController } from '../Controllers/LoadingController';

export interface PermisosIndividualesData
{
	Modulos : Array<Modulo>;
	User : User;
  Group : Group;
  WorkMode : PermisosIndividualesMode;
  Permisos : Array<string>;
}

export enum PermisosIndividualesMode
{
  Usuario = 0,
  Grupo = 1
} 

@Component({
  selector: 'app-permisos-individuales',
  templateUrl: './permisos-individuales.component.html',
  styleUrls: ['./permisos-individuales.component.css', '../Tema.css']
})

export class PermisosIndividualesComponent implements OnInit {

  PermisosG : Array<string> = [];

  constructor(
    private rest : RestService,
    private MessageController : MessageController,
    private LoadingController : LoadingController,
  	public dialogRef: MatDialogRef<PermisosIndividualesData>, 
    @Inject(MAT_DIALOG_DATA) public data: PermisosIndividualesData) { }


  ngOnInit() {
    this.CheckPermisos();

    if(this.data.WorkMode == PermisosIndividualesMode.Usuario)
    {
      if(this.data.User.Grupo != null)
      {
        this.PermisosGrupo(); 
      }
    }
  }

  Close(res : any):void
  {
  	this.dialogRef.close(res);
  }

  PermisosGrupo()
  {
    this.LoadingController.Mostrar();
    this.rest.PermisosGrupo(this.data.User.Grupo.Id).subscribe((result : any)=>
    {
      this.LoadingController.Cerrar(result.message);

      if(result.success)
      {
        this.PermisosG = result.Permisos;
      }
      else
      {
        this.MessageController.Mostrar(result.message, MessageType.Error);
        this.Close(null);
      }
    },
    (ex)=>
    {
      this.LoadingController.Cerrar(ex.statusText);
      this.MessageController.Mostrar(ex.error.message, MessageType.Error);
      this.Close(null);
    });
  }

  CheckPermisos() 
  {
    for (var m = 0; m < this.data.Modulos.length; m++) 
    {
      let modulo : Modulo = this.data.Modulos[m];
      for (var p = 0; p < modulo.Permisos.length; p++) 
      {
        let permiso : Permiso = modulo.Permisos[p];

        permiso.Valor = this.data.Permisos.find(p => permiso.Tag == p) != null;
      }
    }
  }

  Deshabilitar(permiso : Permiso):boolean
  {
    return this.PermisosG.find(p => p == permiso.Tag) != null;
  }

  Seleccionar(permiso : Permiso)
  {
    permiso.Valor = !permiso.Valor;
  }

  Aceptar()
  {
    let res : Array<{Tag : string, Valor : boolean}> = [];

    for (var m = 0; m < this.data.Modulos.length; m++) 
    {
      let modulo : Modulo = this.data.Modulos[m];
      for (var p = 0; p < modulo.Permisos.length; p++) 
      {
        let permiso : Permiso = modulo.Permisos[p];

        res.push({ Tag : permiso.Tag, Valor : permiso.Valor });
      }
    }
    
    if(this.data.WorkMode == PermisosIndividualesMode.Usuario)
    {
      res = res.filter(r => !this.PermisosG.includes(r.Tag));
    }

    this.Close(res.length > 0 ? res : null);
  }

}
