import { Component, OnInit } from '@angular/core';
import {Router} from "@angular/router";
import {NgbModal, ModalDismissReasons} from '@ng-bootstrap/ng-bootstrap';
import {FormControl, FormBuilder, FormGroup, Validators} from "@angular/forms";
import { Location } from '@angular/common';
import { Cards } from '../../Models/Cards';

@Component({
  selector: 'app-lista-etiquetas',
  templateUrl: './lista-etiquetas.component.html',
  styleUrls: ['./lista-etiquetas.component.css']
})
export class ListaEtiquetasComponent implements OnInit {


  closeResult: string;
  validatingForm: FormGroup;

  addTagForm = new FormGroup({

    nombre: new FormControl(),
    color: new FormControl()
  });

  editTagForm = new FormGroup({

    nombre: new FormControl(),
    color: new FormControl()
  });

  constructor(private router: Router, 
              private modalService: NgbModal,
              private _location: Location,
              private formBuilder: FormBuilder) { }

  ngOnInit() {

    this.addTagForm = new FormGroup({
      loginFormModalEmail: new FormControl('', Validators.email),
      loginFormModalPassword: new FormControl('', Validators.required)
    });

    this.addTagForm = this.formBuilder.group({
      id: [],
      nombre: ['', Validators.required],
      color: ['', Validators.required]
    });
  }

  onSubmitAdd(){


  }

  get loginFormModalEmail() {
    return this.validatingForm.get('loginFormModalEmail');
  }

  get loginFormModalPassword() {
    return this.validatingForm.get('loginFormModalPassword');
  }

  backClicked() {
    this._location.back();
  }

  crearEtiqueta(): void{

      this.router.navigate(['crear-etiqueta']);
  }

  editarEtiqueta(): void{

    this.router.navigate(['editar-etiqueta']);
}

  eliminarEtiqueta(etiqueta): void{


}

  openAddModal(content) {

    this.modalService.open(content, {ariaLabelledBy: 'modal-basic-title', 
                                      backdrop: 'static',
                                      size: 'md', 
                                      keyboard: false, 
                                      centered: true
  }).result.then((result) => {

      this.closeResult = `Closed with: ${result}`;

    }, (reason) => {

      this.closeResult = `Dismissed ${this.getDismissReason(reason)}`;

    });

  }

  openEditModal(content) {

    this.modalService.open(content, {ariaLabelledBy: 'modal-basic-title', 
                                      backdrop: 'static',
                                      size: 'md', 
                                      keyboard: false, 
                                      centered: true
  }).result.then((result) => {

      this.closeResult = `Closed with: ${result}`;

    }, (reason) => {

      this.closeResult = `Dismissed ${this.getDismissReason(reason)}`;

    });

  }

  private getDismissReason(reason: any): string {

    if (reason === ModalDismissReasons.ESC) {

      return 'by pressing ESC';

    } else if (reason === ModalDismissReasons.BACKDROP_CLICK) {

      return 'by clicking on a backdrop';

    } else {

      return  `with: ${reason}`;

    }

  }

}
