import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { Group } from '../Models/Group';

export interface GroupInfo
{
	Data : Group;
	Count : number;
  Update : boolean;
}

@Component({
  selector: 'app-group-card',
  templateUrl: './group-card.component.html',
  styleUrls: ['./group-card.component.css']
})
export class GroupCardComponent implements OnInit {

	@Input() Grupo : GroupInfo;
	@Output() edit = new EventEmitter<Group>();
	@Output() transfer = new EventEmitter();
	@Output() permisos = new EventEmitter();

  	constructor() { }
  	ngOnInit() {
  	}

  	GetArray(number, limit)
  	{
  		return new Array(number).slice(0, limit);
  	}

  	Editar()
  	{
  		this.edit.emit(this.Grupo.Data);
  	}

    Permisos()
    {
      this.permisos.emit(this.Grupo.Data)
    }
  }