import { Component, OnInit } from '@angular/core';
import { SystemInfo } from '../SystemInfo';
import { MDevice } from '../Models/Device';
import { RestService } from '../rest.service'
import { HttpErrorResponse } from '@angular/common/http';
import { SecurityUser } from '../Models/SecurityUser';
import { MessageController } from '../Controllers/MessageController';
import { MessageType } from '../Views/Dialog/message/message.component';


@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css']
})

export class LoginComponent implements OnInit {
  host:string;
  Version:string;
  dispositivo:string;
  camisa : string;
  explorador : string;

  SecurityUser : SecurityUser;

  Usuario : string;
  Estacionamiento : string;
  Password : string;
  mensaje : string = "Ingresa tu nombre de usuario y contraseña para iniciar"; 

	constructor(public rest : RestService, private MessageController : MessageController) 
	{  
    localStorage.setItem("cargando","false");
    var systeminfo = new SystemInfo();
    var device = systeminfo.getDevice();

    this.host = window.location.hostname;
    this.Version=localStorage.getItem("Version");
    this.dispositivo = device.OS.Name + " " + device.OS.Version;
    this.explorador = device.Browser.Name;
		localStorage.removeItem("redi");

    // --------------------
    this.SecurityUser = 
          {
            Name: "Code",
            Value: "Code",
            Id: undefined,
            Token: "Code"
          };

    //localStorage.setItem("Usuario",JSON.stringify(this.SecurityUser));
    // --------------------
	}

	Login():void
	{
    var validaciones : boolean[] = 
    [
      this.Usuario ? true : false,
      this.Estacionamiento ? true : false,
      this.Password ? true : false
    ];

    if ( validaciones[0] && validaciones[1] && validaciones[2])
    {
      this.rest.getUser(this.Usuario,this.Password,this.Estacionamiento).subscribe((data : any) =>
      {
        var respuesta = data;
        console.log(respuesta);
        this.mensaje = respuesta.message;

        if(respuesta.success)
        {
          var su = respuesta.userModel;

          this.SecurityUser = 
          {
            Name: su.name,
            Value: su.value,
            Id: su.id,
            Token: su.token
          };

          localStorage.setItem("Usuario",JSON.stringify(this.SecurityUser));

          this.MessageController.Mostrar(data.message, MessageType.Ok);
        }
      }, 

      (response)=>
      {
        this.mensaje = response.error.message;
        if(this.mensaje=="")this.mensaje = ":D";
      });
    }
    else
    {
      this.mensaje = "Favor de llenar todos los campos";
    }
	}

	ngOnInit() { }

}
