import { Component, OnInit, Input } from '@angular/core';
import { Alerta } from '../Models/Alerta';
import { Terminales } from '../Enum/Terminales';


@Component({
  selector: 'app-alert',
  templateUrl: './alert.component.html',
  styleUrls: ['./alert.component.css', '../Tema.css']
})
export class AlertComponent implements OnInit {
  @Input() Alerta : Alerta;
  Icono : String;
  Terminal : String;
  Fecha : String;

  constructor() 
  {

  }

  ngOnInit() {
  	this.Icono = this.getIco(this.Alerta.Terminal.Type);
    this.Terminal = this.getTerminal(this.Alerta.Terminal.Type); 
    this.Fecha = this.getHora(this.Alerta.Fecha);
  }

  getHora(date : Date): String
  {
    return date.getDate() + "/" + (date.getMonth() < 10 ? '0' + date.getMonth() : date.getMonth() ) + 
    "/" + date.getFullYear() + " " + ( date.getHours() < 10 ? '0' +  date.getHours() :  date.getHours()) + 
    ":" + (date.getMinutes() < 10 ? '0' + date.getMinutes() : date.getMinutes());
  }

  getTerminal(terminal : Terminales): String
  {
    return  Terminales[terminal];
  }

  getIco(terminal:Terminales): String
  {
  	var ico;
    switch (terminal) {
      case Terminales.Entrada:
      case Terminales.Salida:
      case Terminales.Servidor:
       ico = "./assets/img/" + Terminales[terminal] + ".png";
       break;
      
      default:
        ico = "./assets/img/CDS.png"
        break;
    }

    return ico;
  }

}
