import { Component, OnInit } from '@angular/core';
import { Opcion } from '../Models/Opcion';

@Component({
  selector: 'app-nav',
  templateUrl: './nav.component.html',
  styleUrls: ['./nav.component.css']
})
export class NavComponent implements OnInit {
	User : any;
	opciones: Opcion[]=
	[
		{nombre:'Reportes', url:'/Reportes/Ingresos', selected:false, imagen:'m_report'},
		{nombre:'Tarifas', url:'/Tarifas', selected:false, imagen:'m_money'},
		{nombre:'Pensiones', url:'/Pensiones', selected:false, imagen:'m_user'},
		{nombre:'Usuarios', url:'/Usuarios', selected:false, imagen:'m_user'},
		{nombre:'Mapa', url:'/Mapa', selected:false, imagen:'map-regular'},
		{nombre:'Monedero', url:'/Monedero', selected:false, imagen:'coins-solid'},
		{nombre:'Estadisticas', url:'/Estadisticas/Ingresos', selected:false, imagen:'m_chart'},
		{nombre:'Seguridad', url:'/Seguridad', selected:false, imagen:'m_shield'}
	];

	ChangeSelection(Opcion : Opcion) {
		var tmp = this.opciones.filter(op => op != Opcion);

		for (var o = tmp.length - 1; o >= 0; o--) 
		{
			var op = tmp[o];
			op.selected = false;
		}

		Opcion.selected = true;
	}

	selectorPos():Number
	{
		var tmp = this.opciones.filter(op => op.selected === true);
		var opcion = tmp[0];

		var pos = this.opciones.indexOf(opcion);
		return ((110 * this.opciones.length)/2) - (110 * pos);
		//((110 * opciones.length)/2)

	}

  constructor() { }

  ngOnInit() { }

  ngAfterContentInit() 
  {
  	var pathname = window.location.pathname;

	var tmp = this.opciones.filter(op => op.selected === true);
	var opcion = tmp[0];

	/*
	Name: su.name,
    Value: su.value,
    Id: su.id,
    Token: su.token*/

	if(opcion == undefined)
	{
		var actual = this.opciones.filter(op => op.url == pathname)[0];
		if(actual != null) this.ChangeSelection(actual);
	}
  }

}
