import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { MatSnackBar } from '@angular/material/snack-bar';
import { User } from '../Models/User';
import { Sexo } from '../Enum/Sexo';
import { Group } from '../Models/Group';
import { RestService } from '../rest.service';



export interface SUser {
  User: User;
  Selected: boolean;
}

export interface SaveData
{
	UserId : Number;
	GroupId : Number;
	newGroupId : Number;
}

@Component({
  selector: 'app-group-transfer',
  templateUrl: './group-transfer.component.html',
  styleUrls: ['./group-transfer.component.css']
})

export class GroupTransferComponent implements OnInit {
	@Input() grupo : Group;

	@Output() close = new EventEmitter();
	@Output() save = new EventEmitter<SaveData[]>();

	Usuarios: SUser[];
	Usuarios2: SUser[];
	constructor(private _snackBar: MatSnackBar, private rest : RestService) { }

	ngOnInit() 
	{
		this.CargarUsuarios();
	}

	Guardar() : void
	{
		let result:SaveData[] = 
		this.Usuarios.filter(u => (u.User.Grupo!=null) ? (u.User.Grupo.Id == this.grupo.Id) :  false).map(u => 
		{ 
			let data:SaveData = {
				UserId: u.User.Id,
				GroupId: (u.User.Grupo != null) ? u.User.Grupo.Id : null,
				newGroupId: null
			};
			return data
		});

		result = result.concat(this.Usuarios2.filter(u => u.User.Grupo == null).map(u => 
		{ 
			let data:SaveData = 
			{
				UserId: u.User.Id,
				GroupId: null,
				newGroupId: this.grupo.Id
			};
			return data
		}));

		this.save.emit(result);
	}

  	CargarUsuarios() : void
  	{
  		localStorage.setItem("cargando","true");
  		this.rest.getActiveUsers().subscribe((data : any) =>
    	{
    		data = data.users.map(u =>
    			{ 
    				var user = new User();
    				user.ActiveTerminal = u.activeTerminal;
					user.Sexo =  u.gender;
					user.RFC =  u.rfc;
					var grupo = null;
					if(u.group != null)
					{
						grupo = new Group();
						grupo.Id = u.group.id;
						grupo.Name = u.group.value;
						grupo.Icon = u.group.icon;
					}
					user.Grupo = grupo;
					user.Password = u.password;
					user.Name = u.name;
					user.User = u.value;
					user.Id = u.id;
					user.Status = u.status;
					user.Url = "assets/svg/profile.svg";

					return user;
    			});

    		localStorage.setItem("cargando","false");

    		this.Usuarios2 = data.filter(u => (u.Grupo != null) ? (u.Grupo.Id == this.grupo.Id) : false).map( u => { return { User: u, Selected: false}});
    		this.Usuarios = data.filter(u => u.Grupo == null).map(u => { return{ User : u, Selected : false }});
    		data = [];
    	},
	    (ex) =>
	    {
	      var mensaje = 
	      {
	        mostrar: true,
	        mensaje: ex.error.message,
	        status: 1
	      };
	      localStorage.setItem("mensaje",JSON.stringify(mensaje));
	      localStorage.setItem("cargando","false");
	      //localStorage.removeItem("Usuario");
	    });
  	}

  Asignar() : void
  {
  	var aux1 = this.Usuarios.filter(u => u.Selected == false);
  	var aux2 = this.Usuarios.filter(u=> u.Selected == true);

  	for (var u = 0; u < aux2.length; u++) 
  	{
  		var user = aux2[u];
  		user.Selected = false;
  	}

  	if(this.Usuarios2 == null)
  	{
  		this.Usuarios2 = aux2;
  	}
  	else
  	{
  		for (var u = 0; u < aux2.length; u++) 
	  	{
	  		var user = aux2[u];
			this.Usuarios2.push(user)
	  	}
  	}

  	this.Usuarios = aux1;
  }

  Desasignar() : void
  {
  	var aux1 = this.Usuarios2.filter(u => u.Selected == false);
  	var aux2 = this.Usuarios2.filter(u=> u.Selected == true);

  	for (var u = 0; u < aux2.length; u++) 
  	{
  		var user = aux2[u];
  		user.Selected = false;
  	}

  	if(this.Usuarios == null)
  	{
  		this.Usuarios = aux2;
  	}
  	else
  	{
  		for (var u = 0; u < aux2.length; u++) 
	  	{
	  		var user = aux2[u];
			this.Usuarios.push(user)
	  	}
  	}

  	this.Usuarios2 = aux1;
  }

  ToogleSelect(user: SUser)
  {
  	user.Selected = !user.Selected;
  }

}
