  getPropertyByPath(obj: Object, pathString: string)
  {
    return pathString.split('.').reduce((o, i) => o[i], obj);
  }

  CalcularTotales():void
  {
    //se guardan los diferentes tipos de eventos que generarón ingresos.
      var tipos = this.dataSource.data.map(d => {return { tipo:d.event.eventType, tarifa: d.rate.type}}).filter((thing, i, a) => a.indexOf(a.find(t => t.tipo == thing.tipo && t.tarifa == thing.tarifa)) === i);

      //se obtiene el total de los ingresos.
      var total = this.dataSource.filteredData.map(d => d.amount.value).reduce((a, b) => a + b, 0);
      this.Total = this.FormatNumber.nuevo(total, '$');
      //this.ChangeIngresos.emit({Total: total});

      //se calculan subtotales para cada tipo de evento
      for (var t = 0; t < tipos.length; t++) 
      {
        // se separan los datos que pertenezcan al tipo de evento actual.
        var tmp = this.dataSource.filteredData.filter(d => d.event.eventType == tipos[t].tipo && d.rate.type == tipos[t].tarifa);
        // se calcula el subtotal de los datos que quedarón y se calcula el porcentaje correspondiente con el total.
        var valor = tmp.map(d => d.amount.value).reduce((a, b) => a + b, 0);
        var p = (100/total * valor).toFixed(2);
        
        // se guardan los totales en el arreglo (se imprime desde el html).
        if(this.Totales.length!=tipos.length)
        {
          this.Totales.push(
          {
            Titulo: this.getTipo(tipos[t].tipo, tipos[t].tarifa) + " "+ this.FormatNumber.nuevo(p, '') +"%",
            Porcentaje: p,
            Valor: this.FormatNumber.nuevo(valor, '$'),
            evento: tipos[t]
          });

          //se ordenan los totales respecto al porcentaje de mayor a menor.
          this.Totales = this.Totales.sort((a, b) => (a.Porcentaje < b.Porcentaje) ? 1 : -1)
        }
        else
        {
          var e = this.Totales.filter(e => e.evento == tipos[t])[0];
          
          e.Titulo= this.getTipo(tipos[t].tipo, tipos[t].tarifa) + " "+ this.FormatNumber.nuevo(p, '') +"%";
          e.Porcentaje= p;
          e.Valor= this.FormatNumber.nuevo(valor, '$');
        }
      }

  }

