import { Component, OnInit, Inject } from '@angular/core';
import {MAT_DIALOG_DATA, MatDialogRef} from "@angular/material";
import {Group} from '../Models/Group';
import {WorkModes} from '../Enum/WorkModes';

export interface GroupModalData
{
  grupo : Group;
  modo : WorkModes;
}

@Component({
  selector: 'app-group-modal',
  templateUrl: './group-modal.component.html',
  styleUrls: ['./group-modal.component.css']
})

export class GroupModalComponent implements OnInit {

  constructor(
    public dialogRef: MatDialogRef<GroupModalComponent>, 
    @Inject(MAT_DIALOG_DATA) public data: GroupModalData) {}

  Grupo : Group;
  WorkMode : WorkModes;

  ngOnInit() 
  {
    this.WorkMode = this.data.modo;

    if(this.WorkMode == WorkModes.Update)
    {
    	this.Grupo = 
    	{
    		Name: this.data.grupo.Name,
    		Status: this.data.grupo.Status,
    		Icon: this.data.grupo.Icon,
    		Description: this.data.grupo.Description,
    		Id: this.data.grupo.Id
    	}
    }
    else
    {
      this.Grupo = 
      {
        Id : null,
        Icon : null,
        Name : "",
        Description : "",
        Status : 1
      }
    }
  }

  Close() : void
  {
  	this.dialogRef.close();
  }

  Aceptar(): Group
  {
    let actualizar : boolean = false;

    if(this.Grupo != null)
    {
      actualizar = 
      this.Grupo.Name == this.data.grupo.Name && 
      this.Grupo.Description == this.data.grupo.Description && 
      this.Grupo.Icon == this.data.grupo.Icon && 
      this.Grupo.Status == this.data.grupo.Status;

      actualizar = !actualizar;
    }
    
    return (actualizar) ? this.Grupo : null;
  }
}
