import { Component, OnInit, Input, Output, EventEmitter} from '@angular/core';
import { Pension } from '../Models/Pension';
import { EstatusPension } from '../Enum/EstatusPension';
import { PermisosController } from '../Controllers/PermisosController';

@Component({
  selector: 'app-monedero-card',
  templateUrl: './monedero-card.component.html',
  styleUrls: ['./monedero-card.component.css']
})

export class MonederoCardComponent implements OnInit {
  @Input() Pension : Pension;

  @Output() Edit = new EventEmitter<Pension>();
  @Output() Delete = new EventEmitter<Pension>();
  @Output() Renew = new EventEmitter<Pension>();
  @Output() Suspend = new EventEmitter<Pension>();
  @Output() Activate = new EventEmitter<Pension>();
  @Output() Select = new EventEmitter<Pension>();
  
  EstatusPension = EstatusPension;

  FechaI : string;
  FechaF : string;
  Estatus =
  {
    text:'',
    value:EstatusPension.Suspendido
  };

  selected : boolean = false;

  constructor(private Permisos : PermisosController) 
  { 
  }

  ngOnInit() 
  {
    this.FechaI = this.getShortDate(this.Pension.Comienzo);
    this.FechaF = this.getShortDate(this.Pension.Vencimiento);

    /*this.Estatus = 
    {
      text: EstatusPension[this.Pension.Estatus],
      value: this.Pension.Estatus
    };*/
  }

  seleccionar()
  {
  	this.selected = !this.selected;
    this.Select.emit(this.Pension);
  }

  getShortDate(date):string
  {
    return new Date(date).toLocaleString("es-ES", 
    { 
      year: '2-digit', 
      month: 'short', 
      day: '2-digit', 
      hour: '2-digit',  
      minute: '2-digit',
      hour12: true 
    }); 
  }

  Eliminar()
  {
    this.Delete.emit(this.Pension);
  }

  Editar()
  {
    this.Edit.emit(this.Pension);
  }
  
  Renovar()
  {
    this.Renew.emit(this.Pension);
  }

  Cancelar()
  {
    this.Suspend.emit(this.Pension);
  }

  Activar()
  {
    this.Activate.emit(this.Pension);
  }

}
