import { AppRoutingModule } from './app-routing.module';
import { AppComponent } from './app.component';
import { RouterModule, Routes } from '@angular/router';
import { BrowserModule } from '@angular/platform-browser';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { NgModule } from '@angular/core';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { HttpClientModule, HttpClientJsonpModule } from '@angular/common/http';
import { NgCircleProgressModule } from 'ng-circle-progress';
import { Ng5SliderModule } from 'ng5-slider';
import { MatBottomSheetModule } from '@angular/material/bottom-sheet';
import { MatStepperModule } from '@angular/material/stepper';
import { NgbModule } from '@ng-bootstrap/ng-bootstrap';/* 
import { ListaEtiquetasModule } from './etiquetas/lista-etiquetas/lista-etiquetas.module';
import { MapaModule } from './mapa/mapa.module'; */

import { 
  MatBadgeModule, MatButtonModule, MatButtonToggleModule, MatCheckboxModule, MatDividerModule,
  MatGridListModule, MatIconModule, MatInputModule, MatMenuModule, MatPaginatorIntl, 
  MatPaginatorModule, MatSidenavModule, MatSortModule, MatTreeModule, MatTooltipModule,  
  MatTableModule, MatToolbarModule,MatProgressSpinnerModule, MatTabsModule, MatListModule,
  MatSnackBarModule, MatSelectModule, MatDialogModule, MatDialogRef} from '@angular/material';

import { HeroesComponent } from './heroes/heroes.component';
import { HeroDetailComponent } from './hero-detail/hero-detail.component';
import { MessagesComponent } from './messages/messages.component';
import { TopBarComponent } from './top-bar/top-bar.component';
import { PanelNotificacionesComponent } from './panel-notificaciones/panel-notificaciones.component';
import { SubMenuComponent } from './sub-menu/sub-menu.component';
import { PensionesComponent } from './pensiones/pensiones.component';
import { NotFoundComponent } from './core/not-found/not-found.component';
import { NavComponent } from './nav/nav.component';
import { LoginComponent } from './login/login.component';
import { CdsTableComponent } from './cds-table/cds-table.component';
import { TarifasComponent } from './tarifas/tarifas.component';
import { TimeLineComponent } from './time-line/time-line.component';
import { DashboardComponent } from './dashboard/dashboard.component';
import { IngresosComponent } from './Reportes/ingresos/ingresos.component';
import { OcupacionesComponent } from './Reportes/ocupaciones/ocupaciones.component';
import { TicketsComponent } from './Reportes/tickets/tickets.component';
import { AlertComponent } from './alert/alert.component';
import { TestComponent } from './test/test.component';
import { PersonasComponent } from './personas/personas.component';
import { PensionCardComponent } from './pension-card/pension-card.component';
import { PensionModalComponent } from './pension-modal/pension-modal.component';
import { CalendarioComponent } from './calendario/calendario.component';

import { LoginGuard } from './CanActivate/login.guard';
import { UsuariosComponent } from './usuarios/usuarios.component';
import { UserCardComponent } from './user-card/user-card.component';
import { UserModalComponent } from './user-modal/user-modal.component';
import { SeguridadComponent } from './seguridad/seguridad.component';
import { GroupCardComponent } from './group-card/group-card.component';
import { GroupTransferComponent } from './group-transfer/group-transfer.component';
import { GroupModalComponent } from './group-modal/group-modal.component';
import { LoadingComponent } from './loading/loading.component';

import { LoadingController } from './Controllers/LoadingController';
import { MessageController } from './Controllers/MessageController';
import { EstadisticasComponent } from './Views/estadisticas/estadisticas.component';
import { AreaChartComponent } from './Charts/area-chart/area-chart.component';
import { EstadisticasIngresosComponent } from './Views/estadisticas/ingresos/estadisticas.ingresos.component';
import { DonutChartComponent } from './Charts/donut-chart/donut-chart.component';
import { AfluenciaComponent } from './Views/estadisticas/afluencia/afluencia.component';
import { InfoPensionComponent } from './Views/Info/Pension/pension.component';
import { YesNoDialogComponent } from './Views/Dialog/yes-no-dialog/yes-no-dialog.component';
import { RenewModalComponent } from './Views/Modals/renew-modal/renew-modal.component';
import { PaymetMethodDialogComponent } from './Views/Dialog/paymet-method-dialog/paymet-method-dialog.component';
import { MessageComponent } from './Views/Dialog/message/message.component';
import { RateCreatorComponent } from './Components/rate-creator/rate-creator.component';
import { PermisosIndividualesComponent } from './permisos-individuales/permisos-individuales.component';
import { RateCardComponent } from './rate-card/rate-card.component';
import { BotonInfoComponent } from './boton-info/boton-info.component';
import { RateModalComponent } from './rate-modal/rate-modal.component';
import { MapaComponent } from './mapa/mapa.component';
import { ListaEtiquetasComponent } from './etiquetas/lista-etiquetas/lista-etiquetas.component';
import { CrearEtiquetaComponent } from './etiquetas/crear-etiqueta/crear-etiqueta.component';
import { EditarEtiquetaComponent } from './etiquetas/editar-etiqueta/editar-etiqueta.component';
import { MonederoComponent } from './monedero/monedero.component';
import { MonederoCardComponent } from './monedero-card/monedero-card.component';
import { MonederoModalComponent } from './monedero-modal/monedero-modal.component';
import { AbonarModalComponent } from './Views/Modals/abonar-modal/abonar-modal.component';
import { MessageService } from './services/message.service'


const appRoutes: Routes = [
  { path:'Login', component:LoginComponent},
  { path:'', canActivate: [LoginGuard], redirectTo:'/Reportes/Ingresos', pathMatch:'full'},
  { path:'Pensiones', canActivate: [LoginGuard], component:PersonasComponent},
  { path:'Tarifas', canActivate: [LoginGuard], component:TarifasComponent},
  { path:'Reportes', canActivate:[LoginGuard], component:DashboardComponent, children: 
    [
      { path:  'Ingresos', component:  IngresosComponent, canActivate:[LoginGuard], data: { permiso:'REPING' }},
      { path:  'Ocupaciones', component:  IngresosComponent, canActivate:[LoginGuard], data: { permiso:'REPOCU' }},
      { path:  'Tickets', component:  TicketsComponent, canActivate:[LoginGuard], data: { permiso:'REPTCK' }},
      { path:  'Pensiones', component:  IngresosComponent, canActivate:[LoginGuard], data: { permiso:'REPPEN' }}
    ]
  },
  { path:'test', canActivate:[LoginGuard], component:TestComponent},
  { path:'Estadisticas', canActivate:[LoginGuard], component:EstadisticasComponent, children: 
    [
      { path:  'Ingresos', component:  EstadisticasIngresosComponent, canActivate:[LoginGuard], data: { permiso:'ESTING' }},
      { path:  'Afluencia', component:  AfluenciaComponent, canActivate:[LoginGuard], data: { permiso:'ESTAFL' }}
    ]
  },
  { path:'Usuarios', canActivate:[LoginGuard], component:UsuariosComponent },
  { path:'Seguridad', canActivate:[LoginGuard], component:SeguridadComponent},
  { path: 'Mapa', canActivate:[LoginGuard], component: MapaComponent},
  { path: 'lista-etiquetas', canActivate:[LoginGuard], component: ListaEtiquetasComponent},
  { path: 'crear-etiqueta', canActivate:[LoginGuard], component: CrearEtiquetaComponent},
  { path: 'editar-etiqueta', canActivate:[LoginGuard], component: EditarEtiquetaComponent},
  { path: 'Monedero', canActivate:[LoginGuard], component: MonederoComponent},
  { path:'**', canActivate:[LoginGuard], component:NotFoundComponent },
];

@NgModule({
  declarations: [
    AppComponent,
    HeroesComponent,
    HeroDetailComponent,
    MessagesComponent,
    TopBarComponent,
    PanelNotificacionesComponent,
    SubMenuComponent,
    PensionesComponent,
    NotFoundComponent,
    NavComponent,
    LoginComponent,
    CdsTableComponent,
    TarifasComponent,
    TimeLineComponent,
    DashboardComponent,
    IngresosComponent,
    OcupacionesComponent,
    TicketsComponent,
    TestComponent,
    AlertComponent,
    PersonasComponent,
    PensionCardComponent,
    PensionModalComponent,
    CalendarioComponent,
    UsuariosComponent,
    UserCardComponent,
    UserModalComponent,
    SeguridadComponent,
    GroupCardComponent,
    GroupTransferComponent,
    GroupModalComponent,
    LoadingComponent,
    EstadisticasComponent,
    AreaChartComponent,
    EstadisticasIngresosComponent,
    DonutChartComponent,
    AfluenciaComponent,
    InfoPensionComponent,
    YesNoDialogComponent,
    RenewModalComponent,
    PaymetMethodDialogComponent,
    MessageComponent,
    RateCreatorComponent,
    PermisosIndividualesComponent,
    RateCardComponent,
    BotonInfoComponent,
    RateModalComponent,
    CrearEtiquetaComponent,
    EditarEtiquetaComponent,
    MonederoComponent,
    MonederoCardComponent,
    MonederoModalComponent,
    AbonarModalComponent,
    MapaComponent,
    ListaEtiquetasComponent
  ],
  imports: [
    BrowserModule,
    BrowserAnimationsModule,
    AppRoutingModule,
    FormsModule,
    ReactiveFormsModule,
    MatTableModule,
    MatPaginatorModule,
    MatSortModule,
    MatTooltipModule,
    MatInputModule,
    MatCheckboxModule,
    MatButtonToggleModule,
    MatToolbarModule,
    MatButtonModule,
    MatSnackBarModule,
    MatProgressSpinnerModule,
    MatTabsModule,
    MatListModule,
    HttpClientModule,
    HttpClientJsonpModule,
    MatIconModule,
    MatGridListModule,
    MatMenuModule,
    MatSelectModule,
    MatStepperModule,
    RouterModule.forRoot(appRoutes),
    Ng5SliderModule,
    MatBottomSheetModule,
    MatTreeModule,
    MatBadgeModule,
    MatDividerModule,
    MatSidenavModule,
    MatDialogModule,
    NgbModule,/* 
    ListaEtiquetasModule,
    MapaModule, */
    NgCircleProgressModule.forRoot(
    {
      "radius": 60,
      "space": -10,
      "outerStrokeWidth": 10,
      "outerStrokeColor": "#891515",
      "innerStrokeColor": "#EEE",
      "innerStrokeWidth": 10,
      "title": "UI",
      "animateTitle": false,
      "animationDuration": 0,
      "showUnits": false,
      "showBackground": false,
      "clockwise": false,
      "startFromZero": false
    })
  ],
  providers: [ MessageService ],
  entryComponents: [
    GroupModalComponent, 
    LoadingComponent,
    UserModalComponent,
    YesNoDialogComponent,
    RenewModalComponent,
    AbonarModalComponent,
    PaymetMethodDialogComponent,
    PensionModalComponent,
    MessageComponent,
    PermisosIndividualesComponent,
    BotonInfoComponent,
    RateModalComponent
  ],  
  bootstrap: [AppComponent]
})

export class AppModule { }
