import { Component, OnInit } from '@angular/core';
import { MAT_DIALOG_DATA, MatDialogRef, MatDialog} from "@angular/material";
import { MessageInformation } from '../Models/MessageInformation';
import { MessageTypes } from "../Enum/MessageTypes";
import { Rate } from '../Models/Rate';

export interface tipo 
{
	Nombre : string;
	val : number;
}


@Component({
  selector: 'app-rate-modal',
  templateUrl: './rate-modal.component.html',
  styleUrls: ['./rate-modal.component.css', '../Tema.css']
})

export class RateModalComponent implements OnInit {

  constructor(
    public dialogRef: MatDialogRef<RateModalComponent>
    ) { }


  Rate : Rate = 
  {
    Id : null,
    Name : null, 
    Type : null,
    apliesTolerance : null,
    MaxAmount : null,
    Status : 1
  };

  Tipos : tipo[] = 
  [
  	{ Nombre : "Pensión", val : 2 },
  	{ Nombre : "Eventual", val : 1 }
  ];

  Mensajes : Array<MessageInformation> = [];

  tipoSelected : tipo = null;

  ngOnInit() 
  {
  	this.tipoSelected = this.Tipos[0];
  }

  Close(thing : any): void
  {
  	this.dialogRef.close(thing);
  }

  Aceptar()
  {
    this.Mensajes = [];
    this.Rate.Type = this.tipoSelected.val;

    //validar nombre
    if(this.Rate.Name == null || this.Rate.Name == "")
    {
      this.Mensajes.push(
      {
        Type : MessageTypes.Warning,
        Message : "La tarifa debe tener un nombre."
      });
    }

    if(this.Mensajes.length == 0)
    {
      this.Close(this.Rate);
    }
  }

}
