import { MatDialog } from "@angular/material";
import { Injectable } from '@angular/core';
import { MessageComponent, MessageType, MessageData } from "../Views/Dialog/message/message.component";

@Injectable({
  providedIn: 'root'
})


export class MessageController
{
	constructor(public dialog: MatDialog) { }

	async Mostrar(message : string, type : MessageType)
	{
		let data : MessageData = 
		{
			Message : message,
			Type : type
		};

		const dialogRef = this.dialog.open(MessageComponent, {
	      width: '350px',
	      disableClose: true,
	      data: data,
	      id: "Message"
	    });
	}
}