import { Injectable } from '@angular/core';

@Injectable({
  providedIn: 'root'
})


export class PermisosController
{
	constructor() { }

	GetPermiso(permiso : string) : boolean
	{
		let permisos : string[] = JSON.parse(localStorage.getItem('Permisos'));

		if(permisos != null)
		{
			return permisos.includes(permiso);
		}
		else
		{
			return false;
		}
	}
}