import { Component, OnInit, Input, ViewChild, ElementRef, OnChanges, SimpleChanges} from '@angular/core';
import { FormatNumber } from '../../Funciones';


import { Observable } from 'rxjs/Observable';
import 'rxjs/add/observable/fromEvent';

export interface info
{
	icon : string;
	color : string;
	text : string;
	rango : string;
	data : Data;
}

export interface Data
{
	valueX: number;
	valueY: string;

	maxValueX: number;
	minValueX: number;

	selected : boolean;
}

export interface Curve
{
	X1 : number;
	Y1 : number;
	X2 : number;
	Y2 : number;

	CPX1 : number;
	CPY1 : number;
	CPX2 : number;
	CPY2 : number;
}


@Component({
  selector: 'app-area-chart',
  templateUrl: './area-chart.component.html',
  styleUrls: ['./area-chart.component.css']
})

export class AreaChartComponent implements OnInit, OnChanges {
	@ViewChild('Chart', { static: false }) public ChartElement: ElementRef;
	@ViewChild('DataInfo', { static: false }) public InfoElement: ElementRef;
	@ViewChild('TotalInfo', {static: false}) public TotalElement : ElementRef;

	@Input() Color : string;
  @Input() Titulo : string;
	@Input() Datos : Array<Data>;

	@Input() Height : number = 400;
	@Input() Width : number = 600;

	@Input() Symbol : string = "";

	FormatNumber = new FormatNumber();

	labelsX : Array<number> = [];
	labelsY : Array<string> = [];

	lineas : Array<Curve> = [];

	Area : string ;

	Rows : number ;

	Info : info =
	{
		color: 'red',
		icon: 'arrow_drop_down',
		text: '',
		rango:'',
		data: null
	}

	ControlPoints : boolean = false;

  	constructor() { }

  	ngOnInit() {
  		this.Height -= 150;
      this.Height = this.Round(this.Height, 50);
      this.Rows = this.Height / 50;
  		this.Width -= 100;

  		if(this.Color == null) this.Color = "#000000";

  		this.CalcularXLabels();
  		this.CalcularYLabels();

  		this.CalcularLinea();
  		this.Area = this.CalcularArea();
		
		//addEventListener('mousemove', this.tellPos(), false);
	}

	ngOnChanges(changes: SimpleChanges) 
  {
    this.doSomething(changes);
  }

  doSomething(datos)
  {
    if(datos.Height!= null)
    {
      if(!datos.Height.firstChange)
      {
        this.Height = Number.parseInt(datos.Height.currentValue) - 150;
        this.Height = this.Round(this.Height, 50);
        this.Rows = this.Height / 50;

        if(datos.Width == null && datos.Datos == null)
        {
          this.CalcularXLabels();
          this.CalcularYLabels();

          this.CalcularLinea();
          this.Area = this.CalcularArea();
        }
      }
    }

    if(datos.Width!= null)
    {
      this.Width = Number.parseInt(datos.Width.currentValue) - 100;

      if(datos.Datos == null)
      {
        this.CalcularXLabels();
        this.CalcularYLabels();

        this.CalcularLinea();
        this.Area = this.CalcularArea();
      }
    }

  	if(datos.Datos != null)
  	{
  		if(!datos.Datos.firstChange)
  		{
  			if(datos.Datos.currentValue != null)
  			{
    			this.CalcularXLabels();
		  		this.CalcularYLabels();

		  		this.CalcularLinea();
		  		this.Area = this.CalcularArea();
	  		}
  		}
  	}
  }

	/*tellPos():any
	{
		this.ShowInfo(null);
		console.log('c');
	}

	offset(el: any) : any
	{
	    var rect = el.getBoundingClientRect();
	    var scrollLeft = window.pageXOffset || document.documentElement.scrollLeft;
	    var scrollTop = window.pageYOffset || document.documentElement.scrollTop;
	    return { top: rect.top + scrollTop, left: rect.left + scrollLeft }
	}*/

	ShowInfo(dato : Data)
	{
		if(dato == null) dato = this.Info.data;

		if(dato != null)
		{
			this.InfoElement.nativeElement.style.visibility = 'visible';

		  	var rect = this.ChartElement.nativeElement.getBoundingClientRect();
		    var scrollLeft = window.pageXOffset || document.documentElement.scrollLeft;
		    var scrollTop = window.pageYOffset || document.documentElement.scrollTop;

		  	var x = 100 + (this.labelsY.indexOf(dato.valueY) * ((this.Width - 100)/(this.labelsY.length - 1)));
		  	var y = this.Height - ((this.Height/this.labelsX[0])) * dato.valueX

		  	this.InfoElement.nativeElement.style.top =  (y + rect.top + scrollTop + 30) + 'px';
		  	this.InfoElement.nativeElement.style.left = (x + rect.left + scrollLeft - 34 - 15) + 'px';

		  	this.TotalElement.nativeElement.innerHTML = this.FormatNumber.nuevo(dato.valueX.toString(), '');

		  	this.Info = (dato.valueX > dato.maxValueX) ?
		  	{
		  		icon : 'trending_up',
		  		color : 'green',
		  		text : this.FormatNumber.nuevo((dato.valueX - dato.maxValueX).toFixed(2), this.Symbol) + ' Arriba del estimado.',
		  		rango : '',
		  		data : dato
		  	}:
		  	(dato.valueX < dato.minValueX)?
		  	{
		  		icon : 'trending_down',
		  		color : 'red',
		  		text : this.FormatNumber.nuevo((dato.minValueX - dato.valueX).toFixed(2), this.Symbol) + ' Debajo del estimado.',
		  		rango : '',
		  		data : dato
		  	}:
		  	{
		  		icon : 'trending_flat',
		  		color : 'gray',
		  		text : 'Dentro del estimado.',
		  		rango : '',
		  		data : dato
		  	}

	  		this.Info.rango = this.FormatNumber.nuevo((dato.minValueX).toFixed(2), this.Symbol) + ' - ' + this.FormatNumber.nuevo((dato.maxValueX).toFixed(2), this.Symbol)
	  	}
	}

	HideInfo()
	{
		this.InfoElement.nativeElement.style.visibility = 'hidden';
	}

  	CalcularArea() : string
  	{
  		let Areatmp1 : Array<Curve> = [];
  		let Areatmp2 : Array<Curve> = [];

  		for (var l = 0; l < this.Datos.length - 1; l++) 
  		{
  			var dato = this.Datos[l];
  			var dato2 = this.Datos[l + 1];

  			var x1 = 100 + (this.labelsY.indexOf(dato.valueY) * ((this.Width - 100)/(this.labelsY.length - 1)));
  			var x2 = 100 + (this.labelsY.indexOf(dato2.valueY) * ((this.Width - 100)/(this.labelsY.length - 1)));

  			var y1 = this.Height - ((this.Height/this.labelsX[0])) * dato.maxValueX;
  			var y2 = this.Height - ((this.Height/this.labelsX[0])) * dato2.maxValueX;

  			var mitadX = ((this.Width - 100)/((this.labelsY.length - 1 ) * 2));
  			var mitadY = y1 - y2;

  			mitadY = ((mitadY < 0) ? mitadY * -1 : mitadY) / 2;

  			var mitad = (mitadY > mitadX) ? mitadX : mitadY;

  			var cpx1 = x1 + mitad;
  			var cpy1 = y1;

  			var cpx2 = x2 - mitad;
  			var cpy2 = y2;

  			if(l > 0 )
  			{
  				var datoA = this.Datos[ l-1 ];
  				var yA = this.Height - ((this.Height/this.labelsX[0])) * datoA.maxValueX;

  				if((yA < y1 !== y2 < y1 ) && (yA > y1 !== y2 > y1 ))
  				{
  					cpy1 = (dato.maxValueX > dato2.maxValueX) ? cpy1 + mitad : (dato.maxValueX < dato2.maxValueX) ? cpy1 - mitad : cpy1;
  				}
  			}
  			else
  			{
  				cpy1 = (dato.maxValueX > dato2.maxValueX) ? cpy1 + mitad : (dato.maxValueX < dato2.maxValueX) ? cpy1 - mitad : cpy1;
  			}

			if(l < this.Datos.length - 2 )
  			{
  				var datoS = this.Datos[l+2];
  				var yS = this.Height - ((this.Height/this.labelsX[0])) * datoS.maxValueX;

				if((y1 > y2 !== yS > y2 ) && (y1 < y2 !== yS < y2 ))
  				{
  					cpy2 = (dato.maxValueX < dato2.maxValueX) ? cpy2 + mitad : (dato.maxValueX > dato2.maxValueX) ? cpy2 - mitad : cpy2;
  				}
  			}
  			else
  			{
  				cpy2 = (dato.maxValueX < dato2.maxValueX) ? cpy2 + mitad : (dato.maxValueX > dato2.maxValueX) ? cpy2 - mitad : cpy2;
  			}

  			Areatmp1.push({
  				X1 : x1,
  				Y1 : y1,
  				X2 : x2,
  				Y2 : y2,
  				CPX1 : cpx1,
  				CPY1 : cpy1,
  				CPX2 : cpx2,
  				CPY2 : cpy2
  			});
  		}

  		var tmp = this.Datos.reverse();

  		for (var l = 0; l < tmp.length - 1; l++) 
  		{
  			var dato = tmp[l];
  			var dato2 = tmp[l + 1];

  			var x1 = 100 + (this.labelsY.indexOf(dato.valueY) * ((this.Width - 100)/(this.labelsY.length - 1)));
  			var x2 = 100 + (this.labelsY.indexOf(dato2.valueY) * ((this.Width - 100)/(this.labelsY.length - 1)));

  			var y1 = this.Height - ((this.Height/this.labelsX[0])) * dato.minValueX;
  			var y2 = this.Height - ((this.Height/this.labelsX[0])) * dato2.minValueX;

  			var mitadX = ((this.Width - 100)/((this.labelsY.length - 1 ) * 2));
  			var mitadY = y1 - y2;

  			mitadY = ((mitadY < 0) ? mitadY * -1 : mitadY) / 2;

  			var mitad = (mitadY > mitadX) ? mitadX : mitadY;

  			var cpx1 = x1 - mitad;
  			var cpy1 = y1;

  			var cpx2 = x2 + mitad;
  			var cpy2 = y2;

  			if(l > 0 )
  			{
  				var datoA = tmp[ l-1 ];
  				var yA = this.Height - ((this.Height/this.labelsX[0])) * datoA.minValueX;

  				if((yA < y1 !== y2 < y1 ) && (yA > y1 !== y2 > y1 ))
  				{
  					cpy1 = (dato.minValueX > dato2.minValueX) ? cpy1 + mitad : (dato.minValueX < dato2.minValueX) ? cpy1 - mitad : cpy1;
  				}
  			}
  			else
  			{
  				cpy1 = (dato.minValueX > dato2.minValueX) ? cpy1 + mitad : (dato.minValueX < dato2.minValueX) ? cpy1 - mitad : cpy1;
  			}



			if(l < this.Datos.length - 2 )
  			{
  				var datoS = tmp[l+2];
  				var yS = this.Height - ((this.Height/this.labelsX[0])) * datoS.minValueX;

				if((y1 > y2 !== yS > y2 ) && (y1 < y2 !== yS < y2 ))
  				{
  					cpy2 = (dato.minValueX < dato2.minValueX) ? cpy2 + mitad : (dato.minValueX > dato2.minValueX) ? cpy2 - mitad : cpy2;
  				}
  			}
  			else
  			{
  				cpy2 = (dato.minValueX < dato2.minValueX) ? cpy2 + mitad : (dato.minValueX > dato2.minValueX) ? cpy2 - mitad : cpy2;
  			}

  			Areatmp2.push({
  				X1 : x1,
  				Y1 : y1,
  				X2 : x2,
  				Y2 : y2,
  				CPX1 : cpx1,
  				CPY1 : cpy1,
  				CPX2 : cpx2,
  				CPY2 : cpy2
  			});
  		}

  		var restmp = Areatmp1.map((c, i) => ((i == 0) ? 'M ' + c.X1 + ' ' + c.Y1 + ' C ' : ' ') + c.CPX1 + ' ' + c.CPY1 + ', ' + c.CPX2 + ' ' + c.CPY2 + ', ' + c.X2 + ' ' + c.Y2 + '');
  		var res = (restmp.length > 0) ? restmp.reduce((acumulado, actual) => acumulado + actual) : "";

  		res = (Areatmp2.length > 0 ) ? res + " V " + Areatmp2[0].Y1 + " " : res;

  		restmp = Areatmp2.map((c, i) => ((i == 0) ? ' C ' : ' ') + c.CPX1 + ' ' + c.CPY1 + ', ' + c.CPX2 + ' ' + c.CPY2 + ', ' + c.X2 + ' ' + c.Y2 + '');
      res = (restmp.length>0) ? res  + restmp.reduce((acumulado, actual) => acumulado + actual): res;

  		res = res + " Z ";
  		return res;
  	}

  	CalcularXLabels()
  	{
  		this.labelsX = [];
  		let tmp : Array<number> = this.Datos.map(d => d.valueX).concat(this.Datos.map(d => d.maxValueX)).concat(this.Datos.map(d => d.minValueX));
  		var max = Math.max.apply(null, tmp);

      max = ((max / this.Height) * 15) + max;
  		max = this.Round(max, (this.Rows * 10) - 10);
  		var dif = max / (this.Rows - 1);
  		tmp = null;

  		for (var r = 0; r < this.Rows; r++) 
  		{
  			this.labelsX.push(max);
  			max-= dif;
  		}
  	}

  	CalcularYLabels()
  	{
  		this.labelsY = [];
  		let tmp : Array<string> = this.Datos.map(d => d.valueY).filter((v, i , a) => a.indexOf(v) == i);
  		this.labelsY = tmp;
  	}

  	CalcularLinea()
  	{
  		this.lineas = [];
  		for (var l = 0; l < this.Datos.length - 1; l++) 
  		{
  			var dato = this.Datos[l];
  			var dato2 = this.Datos[l + 1];

  			var x1 = 100 + (this.labelsY.indexOf(dato.valueY) * ((this.Width - 100)/(this.labelsY.length - 1)));
  			var x2 = 100 + (this.labelsY.indexOf(dato2.valueY) * ((this.Width - 100)/(this.labelsY.length - 1)));

  			var y1 = this.Height - ((this.Height/this.labelsX[0])) * dato.valueX;
  			var y2 = this.Height - ((this.Height/this.labelsX[0])) * dato2.valueX;

  			var mitadX = ((this.Width - 100)/((this.labelsY.length - 1 ) * 2));
  			var mitadY = y1 - y2;

  			mitadY = ((mitadY < 0) ? mitadY * -1 : mitadY) / 2;

  			var mitad = (mitadY > mitadX) ? mitadX : mitadY;

  			var cpx1 = x1 + mitad;
  			var cpy1 = y1;

  			var cpx2 = x2 - mitad;
  			var cpy2 = y2;

  			if(l > 0 )
  			{
  				var datoA = this.Datos[ l-1 ];
  				var yA = this.Height - ((this.Height/this.labelsX[0])) * datoA.valueX;

  				if((yA < y1 !== y2 < y1 ) && (yA > y1 !== y2 > y1 ))
  				{
  					cpy1 = (dato.valueX > dato2.valueX) ? cpy1 + mitad : (dato.valueX < dato2.valueX) ? cpy1 - mitad : cpy1;
  				}
  			}
  			else
  			{
  				cpy1 = (dato.valueX > dato2.valueX) ? cpy1 + mitad : (dato.valueX < dato2.valueX) ? cpy1 - mitad : cpy1;
  			}

			  if(l < this.Datos.length - 2 )
  			{
  				var datoS = this.Datos[l+2];
  				var yS = this.Height - ((this.Height/this.labelsX[0])) * datoS.valueX;

				  if((y1 > y2 !== yS > y2 ) && (y1 < y2 !== yS < y2 ))
  				{
  					cpy2 = (dato.valueX < dato2.valueX) ? cpy2 + mitad : (dato.valueX > dato2.valueX) ? cpy2 - mitad : cpy2;
  				}
  			}
  			else
  			{
  				cpy2 = (dato.valueX < dato2.valueX) ? cpy2 + mitad : (dato.valueX > dato2.valueX) ? cpy2 - mitad : cpy2;
  			}

  			this.lineas.push({
  				X1 : x1,
  				Y1 : y1,
  				X2 : x2,
  				Y2 : y2,
  				CPX1 : cpx1,
  				CPY1 : cpy1,
  				CPX2 : cpx2,
  				CPY2 : cpy2
  			});
  		}
  	}

  	Round(num : number, multipleOf : number) : number
  	{
      num = (num == 0) ? 1 : num;
  		num += multipleOf;
        return Math.ceil(num/multipleOf)*multipleOf;
    }

    Select(dato: Data, valor: boolean) : void
    {
    	dato.selected = valor;
    	if(dato.selected)
    	{
    		this.ShowInfo(dato);
    	}
    }

    Format(number : number) : string
    {	
    	return this.FormatNumber.nuevo(number, this.Symbol);
    }

    /*showCoords(event) : number
    {
	  ;var x = event.clientX;
	  var y = event.clientY;
	  var coords = "X coords: " + x + ", Y coords: " + y;
	  return x
	}*/
}
