import { Component, OnInit, Output, EventEmitter, Input } from '@angular/core';
import { Meses, Mes } from '../Models/Interfaces';


export interface fechas
{
  fechaI : Date;
  fechaF : Date;
}

@Component({
  selector: 'app-calendario',
  templateUrl: './calendario.component.html',
  styleUrls: ['./calendario.component.css']
})
export class CalendarioComponent implements OnInit {
  @Output() Cancelar = new EventEmitter();
  @Output() Aplicar = new EventEmitter<fechas>();

  Meses : Array<Mes> = Meses;
	/*Meses = : Array<any> = 
	[
		{ valor: 0, nombre: "Enero", nombreCorto: "Ene", selected: false },
		{ valor: 1, nombre: "Febrero", nombreCorto: "Feb", selected: false },
		{ valor: 2, nombre: "Marzo", nombreCorto: "Mar", selected: false },
		{ valor: 3, nombre: "Abril", nombreCorto: "Abr", selected: false },
		{ valor: 4, nombre: "Mayo", nombreCorto: "May", selected: false },
		{ valor: 5, nombre: "Junio", nombreCorto: "Jun", selected: false },
		{ valor: 6, nombre: "Julio", nombreCorto: "Jul", selected: false },
		{ valor: 7, nombre: "Agosto", nombreCorto: "Ago", selected: false },
		{ valor: 8, nombre: "Septiembre", nombreCorto: "Sep", selected: false },
		{ valor: 9, nombre: "Octubre", nombreCorto: "Oct", selected: false },
		{ valor: 10, nombre: "Noviembre", nombreCorto: "Nov", selected: false },
		{ valor: 11, nombre: "Diciembre", nombreCorto: "Dic", selected: false }
	];*/

  NombreDias : Array<any> =
  [
    { valor: 0, nombre: "Dom"},
    { valor: 1, nombre: "Lun"},
    { valor: 2, nombre: "Mar"},
    { valor: 3, nombre: "Mié"},
    { valor: 4, nombre: "Jue"},
    { valor: 5, nombre: "Vie"},
    { valor: 6, nombre: "Sáb"}
  ];

	Dias : Array<any> = [];
	Anos : Array<any> = [];

  @Input() FechaI : Date;
  @Input() FechaF : Date;

  @Input() Top : number = 100;
  @Input() Left : number = 100;

  @Input() Range : boolean = true;

  PMonth : boolean = true;
  NMonth : boolean = true;

  Mes : Mes;
  Ano : number;

  Hide : Boolean = true;
  PreSelect : Number = 0;

  Time =
  {
    I : 
    {
      Hora : 0,
      Min : 0
    },
    F : 
    {
      Hora : 23,
      Min : 59
    }
  }

  SelectInicio: Boolean = true;

  constructor() { }

  ngOnInit() 
  {
  	this.CargarAños();

  	var mes = Meses.filter(m => m.Selected == true)[0];
  	var año = this.Anos.filter(a => a.selected == true)[0];

    this.Mes = mes;
    this.Ano = año.valor;
    this.ValidarAcciones();

  	this.CargarDias((mes == null) ? null: mes.Valor, (año == null) ? null : año.valor);
  }

  minInicio() : void
  {
    if(this.Time.I.Min <= 0)
    {
      if(this.Time.I.Hora <= 0)
      {
        this.Time.I.Hora = 0;
        this.Time.I.Min = 0;
      }
      else
      {
        this.Time.I.Hora -= 1;
        this.Time.I.Min = 59;
      }
    }
    else
    {
      if(this.Time.I.Min >= 59)
      {
        if(this.Time.I.Hora >= 23)
        {
          this.Time.I.Hora = 23;
          this.Time.I.Min = 59;
        }
        else
        {
          this.Time.I.Hora += 1;
          this.Time.I.Min = 0;
        }
      }
    }
  }

  horaInicio() : void
  {
    if(this.Time.I.Hora < 0)
    {
      this.Time.I.Hora = 23;
    }

    if(this.Time.I.Hora > 23)
    {
      this.Time.I.Hora = 0;
    }
  }

  minFin() : void
  {
    if(this.Time.F.Min <= 0)
    {
      if(this.Time.F.Hora <= 0)
      {
        this.Time.F.Hora = 0;
        this.Time.F.Min = 0;
      }
      else
      {
        this.Time.F.Hora -= 1;
        this.Time.F.Min = 59;
      }
    }
    else
    {
      if(this.Time.F.Min >= 59)
      {
        if(this.Time.F.Hora >= 23)
        {
          this.Time.F.Hora = 23;
          this.Time.F.Min = 59;
        }
        else
        {
          this.Time.F.Hora += 1;
          this.Time.F.Min = 0;
        }
      }
    }
  }

  horaFin() : void
  {
    if(this.Time.F.Hora < 0)
    {
      this.Time.F.Hora = 23;
    }

    if(this.Time.F.Hora > 23)
    {
      this.Time.F.Hora = 0;
    }
  }

  SelectDay(fecha: Date)
  {
    if(this.Range)
    {
      if(this.SelectInicio == true) 
      {
        this.FechaI = fecha;
        this.FechaI.setHours(0, 0, 0);
        this.SelectInicio = false;
        this.PreSeleccion(-1);
      }
      else
      {
        this.FechaF = fecha;
        this.FechaF.setHours(23, 59, 59);
        this.SelectInicio = true;
      }
    }
    else
    {
      this.FechaI = fecha;
      this.FechaI.setHours(0, 0, 0);
      this.FechaF = fecha;
      this.FechaF.setHours(23, 59, 59);
    }
  }

  Aceptar()
  {

    this.FechaI.setHours(this.Time.I.Hora, this.Time.I.Min, 0);
    this.FechaF.setHours(this.Time.F.Hora, this.Time.F.Min, 59);

    let fechas:fechas =
    {
      fechaI: this.FechaI,
      fechaF: this.FechaF
    };



    this.Aplicar.emit(fechas);
  }

  CargarAños():void
  {
  	var añoactual = new Date().getFullYear();
  	for (var a = 2019; a <= añoactual; a++) 
  	{
  		this.Anos.push(
  		{
  			valor: a,
  			selected: false
  		});
  	}

    if(this.Ano == null) 
    {
      this.Anos.filter(a=> a.valor == new Date().getFullYear())[0].selected = true;
      Meses.filter(m=> m.Valor == new Date().getMonth())[0].Selected = true;
    }
  }

  CambiarMes() : void
  {
    this.CargarDias(this.Mes.Valor, this.Ano);
  }

  CargarDias(mes, año):void
  {
    this.Dias = [];
  	mes = (mes == null) ? new Date().getMonth() : mes;
  	año = (año == null) ? new Date().getFullYear() : año;

  	var date = new Date(año, mes, 1);

    if(this.FechaI == null)
    {
      var tmp = new Date();
      this.FechaI = (mes != new Date().getMonth()) ? new Date(date.getTime()) : new Date(tmp.getFullYear(), tmp.getMonth(), tmp.getDate());
      this.FechaF = (mes != new Date().getMonth()) ? new Date(date.getTime()) : new Date(tmp.getFullYear(), tmp.getMonth(), tmp.getDate());
      this.FechaF.setHours(23, 59, 59);
      this.FechaI.setHours(0, 0, 0);
      this.ValidarAcciones();
    }

    var diasAntes = new Date(año, mes, 1).getDay();
    var diasdelmes = new Date(año, mes + 1, 0).getDate();
    var totaldias = diasdelmes + (42 - diasdelmes - diasAntes);

    for (var d = (1 - diasAntes); d <= totaldias; d++) 
    {
      var date = new Date(año, mes, d);
      date.setHours(0, 0, 0);
      this.Dias.push(
      {
        valor: date,
        numero: date.getDate(),
        weekday: date.getDay(),
        mes: Meses[date.getMonth()]
      })
    }

    this.ValidarAcciones();
  }

  GetShortDate(fecha) : string
  {
    let f:Date = new Date(fecha);
    return this.NombreDias[f.getDay()].nombre + "., " + f.getDate() + " " + Meses[f.getMonth()].NombreCorto + ". "+ f.getFullYear();
  }

  CompareDates(fecha1 : Date, fecha2 : Date) : boolean
  {
    var f1 = new Date(fecha1.getFullYear(), fecha1.getMonth(), fecha1.getDate());

    var f2 = new Date(fecha2.getFullYear(), fecha2.getMonth(), fecha2.getDate());

    return f1.getTime() == f2.getTime();
  }

  PreSeleccion(modo : Number) : void
  {
    /*
      modos:
       -1 - Personalizado
        0 - Hoy
        1 - 7 días
        2 - 15 días
        3 - Este mes
    */

    var tmp = new Date();
    switch (modo) {
      case 0:
        this.Ano = tmp.getFullYear();
        this.Mes = Meses.filter(m => m.Valor == tmp.getMonth())[0];
        this.CargarDias(tmp.getMonth(), tmp.getFullYear());
        this.FechaI = new Date(tmp.getFullYear(), tmp.getMonth(), tmp.getDate());
        this.FechaF = new Date(tmp.getFullYear(), tmp.getMonth(), tmp.getDate());
        this.SelectInicio = true;
      break;
      case 1:
        this.Ano = tmp.getFullYear();
        this.Mes = Meses.filter(m => m.Valor == tmp.getMonth())[0];
        this.CargarDias(tmp.getMonth(), tmp.getFullYear());
        this.FechaI = new Date(tmp.getFullYear(), tmp.getMonth(), tmp.getDate() - 6);
        this.FechaF = new Date(tmp.getFullYear(), tmp.getMonth(), tmp.getDate());
        this.SelectInicio = true;
      break;
      case 2:
        this.Ano = tmp.getFullYear();
        this.Mes = Meses.filter(m => m.Valor == tmp.getMonth())[0];
        this.CargarDias(tmp.getMonth(), tmp.getFullYear());
        this.FechaI = new Date(tmp.getFullYear(), tmp.getMonth(), tmp.getDate() - 14);
        this.FechaF = new Date(tmp.getFullYear(), tmp.getMonth(), tmp.getDate());
        this.SelectInicio = true;
      break;
      case 3:
        this.Ano = tmp.getFullYear();
        this.Mes = Meses.filter(m => m.Valor == tmp.getMonth())[0];
        this.CargarDias(tmp.getMonth(), tmp.getFullYear());
        this.FechaI = new Date(tmp.getFullYear(), tmp.getMonth(), 1);
        this.FechaF = new Date(tmp.getFullYear(), tmp.getMonth() + 1, 0);
        this.SelectInicio = true;
      break;
      
      default: break;
    }

    this.PreSelect = modo;
  }

  PreviusMonth() : void
  {
    var date = new Date(this.Ano, this.Mes.Valor - 1, 1);
    Meses.filter(m => m.Selected == true)[0].Selected = false;
    Meses.filter(m => m.Valor == date.getMonth())[0].Selected = true;
    this.Mes = Meses.filter(m => m.Selected == true)[0];
    this.Ano = this.Anos.filter(a => a.valor == date.getFullYear())[0].valor;
    this.CargarDias(this.Mes.Valor, this.Ano);
  }

  NextMonth() : void
  {
    var date = new Date(this.Ano, this.Mes.Valor + 1, 1);
    Meses.filter(m => m.Selected == true)[0].Selected = false;
    Meses.filter(m => m.Valor == date.getMonth())[0].Selected = true;
    this.Mes = Meses.filter(m => m.Selected == true)[0];
    this.Ano = this.Anos.filter(a => a.valor == date.getFullYear())[0].valor;
    this.CargarDias(this.Mes.Valor, this.Ano);
  }

  ValidarAcciones() : void
  {
    var maxYear = this.Anos[this.Anos.length - 1].valor;
    var minYear = this.Anos[0].valor;

    var maxMes = 11;
    var minMes = 0;

    if(this.Mes.Valor == minMes && this.Ano == minYear) this.PMonth = true;
    else this.PMonth = false;

    if(this.Mes.Valor == maxMes && this.Ano == maxYear) this.NMonth = true;
    else this.NMonth = false;
  }

  ToogleHide() : void
  {
    this.Hide = !this.Hide;
  }
}
