import { Component, OnInit } from '@angular/core';
import {MatBottomSheet, MatBottomSheetRef} from '@angular/material/bottom-sheet';

export interface PaymentMethod {
  Nombre: string;
  Descripcion : string;
  Icono : string;
  Valor : string;
}


@Component({
  selector: 'app-paymet-method-dialog',
  templateUrl: './paymet-method-dialog.component.html',
  styleUrls: ['./paymet-method-dialog.component.css','../../../Tema.css']
})

export class PaymetMethodDialogComponent implements OnInit {
	PaymentMethods: PaymentMethod[] = [
    {
      Nombre: "Efectivo",
      Descripcion: null,
      Icono : "cash",
      Valor : "Efectivo"
    },
    {
      Nombre: "Transferencia bancaria o Deposito",
      Descripcion: null,
      Icono : "receipt",
      Valor : "Transferencia"
    },
    {
      Nombre: "Tarjeta Credito / Debito",
      Descripcion: null,
      Icono : "credit-card",
      Valor : "Tarjeta"
    }
  ];

  constructor(public dialogRef: MatBottomSheetRef<PaymetMethodDialogComponent>) { }

  ngOnInit() {
  }

  Pago(metodo : PaymentMethod)
  {
  	this.dialogRef.dismiss(metodo.Valor);
  }

}
