import { Component, OnInit, Input, Output, EventEmitter} from '@angular/core';
import { Rate } from '../Models/Rate';

@Component({
  selector: 'app-rate-card',
  templateUrl: './rate-card.component.html',
  styleUrls: ['./rate-card.component.css']
})
export class RateCardComponent implements OnInit {
	@Output() Select = new EventEmitter<Rate>();
	@Output() Delete = new EventEmitter<Rate>();

	@Input() Selected : boolean = false;
	@Input() Rate : Rate;

	constructor() { }
	ngOnInit() { }

	GetTipo(type : number) : string
	{
		switch (type) {
			case 1: return 'Eventual'; break;
			case 2: return 'Pensión'; break;
			case 4: return 'Bolsa de tiempo'; break;
			
			default: return 'Desconocida';break;
		}
	}

	Selecionar()
	{
		this.Select.emit(this.Rate);
	}

	Eliminar()
	{
		this.Delete.emit(this.Rate);
	}
}
