import { Component, OnInit, ChangeDetectionStrategy } from '@angular/core';
import { Terminales } from './Enum/Terminales';
import { Observable, of } from 'rxjs'
import { CanActivate, Router } from '@angular/router'

Notification.requestPermission();

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css'],
  changeDetection: ChangeDetectionStrategy.OnPush
})

export class AppComponent {
  zoom = false;
  mensaje=
  {
    texto:"mensaje de prueba de error",
    mostrar: false,
    status: 1
  };
 socket;
 scheme = document.location.protocol == "https:" || true ? "wss" : "ws";
 port = document.location.port ? (":" + document.location.port) : "";
 connectionUrl = this.scheme + "://pepinosbox.club:81/api/SCL/Alert/Subscribe";
 count : number = 0;
	Mensajes = [];
	public Title = 'SoftCloud Lite';
	public Version = '1.0.1 Alpha'; // [Final] . [Funcionalidades] . [Errores] [Estabilidad]

  constructor(private router: Router) 
  {
    localStorage.setItem("Title", this.Title);
    localStorage.setItem("Version", this.Version);
    localStorage.setItem("cargando", "false");

    this.connectButton();
  }

  menu()
  {
    this.zoom = true;
  }
  cerrar()
  {
    this.zoom = false;
  }

  checkCarga():boolean
  {
    var cargando = localStorage.getItem("cargando");
    
    if(cargando != null) 
    {
      return (cargando == "true");
    }
    else return false;
  }

  checkMensaje():boolean
  {
    var m = JSON.parse(localStorage.getItem("mensaje"));
    if(m)
    {
      this.mensaje.texto = m.mensaje;
      this.mensaje.mostrar = m.mostrar;
      this.mensaje.status = m.status;
    }
    return (this.mensaje.mostrar == true);
  }

  cerrarMensaje():void
  {
    var m = JSON.parse(localStorage.getItem("mensaje"));
    this.mensaje.mostrar = false;
    this.mensaje.status = 2;
    localStorage.setItem("mensaje",JSON.stringify(this.mensaje));
  }

  checkLogin():boolean
  {
    //var notificaciones = localStorage.getItem("Notificaciones");
    //notificaciones = (notificaciones!=null)?((parseInt(notificaciones)>0)?"("+notificaciones+") ":""):"";
    document.title = //notificaciones +
    localStorage.getItem("Title") + " | " +
    window.location.pathname.replace('/', "");

    var usuario = localStorage.getItem("Usuario");


      if(usuario!=undefined)
      {
        if(window.location.pathname=='/Login') 
        {
          console.log('redireccion '+window.location.href);
          this.router.navigate(['./']);
        }
        return true;
      }
      else
      {
        if(window.location.pathname!='/Login') 
        {
          var oldUrl = localStorage.getItem("redi");
          if(oldUrl!=window.location.href)
          {
            localStorage.setItem("redi", window.location.href);
            console.log('redireccion login ' + window.location.href);
            this.router.navigate(['./Login']);
          }
        }
        return false;
      }
    //return true;
  }

  getHeroes():string
  {
    return this.Title;
  }

  noti(message:string, terminal:Terminales):void
  {
    var ico;
    switch (terminal) {
      case Terminales.Entrada:
      case Terminales.Salida:
      case Terminales.Servidor:
       ico = "./assets/img/" + Terminales[terminal] + ".png";
       break;
      
      default:
        ico = "./assets/img/CDS.png"
        break;
    }
    var extra = {
      icon: ico,
      body: message
    }

  	var notif = new Notification(Terminales[terminal], extra);

  	/*notif.addEventListener("show",  ()=>{this.mensaje("Show "+Terminales[terminal])});
  	notif.addEventListener("click", ()=>{this.mensaje("click en "+Terminales[terminal])});
  	notif.addEventListener("close", ()=>{this.mensaje("close "+Terminales[terminal])});
  	notif.addEventListener("error", ()=>{this.mensaje("error en "+Terminales[terminal])});*/
  }

  /*mensaje(mensaje:string):void
  {
  	this.Mensajes.unshift(mensaje);
  }*/

  closeButton() :void
  {
    if (!this.socket || this.socket.readyState != WebSocket.OPEN) 
    {
      alert("socket not connected");
    }
    this.socket.close(1000, "Closing from client");
  }

  sendButton():void
  {
    if (!this.socket || this.socket.readyState != WebSocket.OPEN) 
    {
      alert("socket not connected");
    }

    var data = "Desde Página 😙";
    this.socket.send(data);
  }

  updateState():void
  {
      if (!this.socket) 
      {
        //disable();
      } 
      else 
      {
          switch (this.socket.readyState) 
          {
              case WebSocket.CLOSED:
              //disable();
              break;
              case WebSocket.CLOSING:
              //disable();
              break;
              case WebSocket.CONNECTING:
              //disable();
              break;
              case WebSocket.OPEN:
              //enable();
              break;
              default:
              //disable();
              break;
          }
      }
  }

    onopen(event) : void
    {
      console.log("Connection opened");
    };

    onclose (event) : void 
    {
      localStorage.setItem("Sockets", "offline");
      console.log('Connection closed:');
      console.log(event);
    };

    onmessage (event) : void{
      //this.Mensajes.unshift(event.data);
      this.noti(event.data, Math.floor(Math.random() * 4) + 1);
    };

    onerror(event) : void
    {
      console.log("error:");
      console.log(event);
    };

  connectButton():void
  {
    localStorage.setItem("Sockets", "online");
    var usuario = JSON.parse(localStorage.getItem("Usuario"));
    if(usuario!=null)
    {
      var get = "?token=" + usuario.Token + "&&user=" + usuario.Value;

      this.socket = new WebSocket(this.connectionUrl + get);

      this.socket.onclose = ()=> { this.onclose(event) };
      this.socket.onmessage = ()=> { this.onmessage(event) };
      this.socket.onopen = ()=> { this.onopen(event) };
      this.socket.onerror = ()=> { this.onerror(event) };
    }
};
}




