import { Component, OnInit, Inject } from '@angular/core';
import { Sexo } from '../Enum/Sexo';
import { MAT_DIALOG_DATA, MatDialogRef} from "@angular/material";
import { User } from '../Models/User';
import { Email } from '../Models/Email';
import { WorkModes} from '../Enum/WorkModes';
import { MessageInformation } from '../Models/MessageInformation';
import { MessageTypes } from "../Enum/MessageTypes";


export interface UserModalData
{
  User : User;
  Modo : WorkModes;
  Email : Email;
}

@Component({
  selector: 'app-user-modal',
  templateUrl: './user-modal.component.html',
  styleUrls: ['./user-modal.component.css', '../Tema.css']
})

export class UserModalComponent implements OnInit {

	User: User;
  Email : Email;

	keys : Array<any>;

  Mensajes : Array<MessageInformation> = [];

  constructor(
  	public dialogRef: MatDialogRef<UserModalComponent>, 
    @Inject(MAT_DIALOG_DATA) public data: UserModalData) { }

  ngOnInit() {
    var key = Object.keys(Sexo);
    var val = Object.values(Sexo);
    this.keys = key.map((k, i) => {return {name: k, valor: val[i]}}).slice(key.length / 2);//;

    if(this.data.Modo == WorkModes.Add)
    {
      this.User = 
      {
        Name: null,
        Password: null,
        User: null,
        Sexo: null,
        Grupo: null,
        ActiveTerminal: null, 
        RFC: null, 
        Id: null, 
        Status: null, 
        Url: null,
        Correos: null
      }

      this.Email = 
      {
        Id : null,
        Address : ""
      }
    }

    if(this.data.Modo == WorkModes.Update)
    {
  	  this.User = 
  	  {
  		  Name: this.data.User.Name,
        Password: this.data.User.Password,
        User: this.data.User.User,
        Sexo: this.data.User.Sexo,
        Grupo: this.data.User.Grupo,
        ActiveTerminal: this.data.User.ActiveTerminal, 
        RFC: this.data.User.RFC, 
        Id: this.data.User.Id, 
        Status: this.data.User.Status, 
        Url: this.data.User.Url,
        Correos: null
    	}

      this.Email = 
      {
        Id : this.data.Email.Id,
        Address : this.data.Email.Address
      }
    }
  }

  Close(res : any): void
  {
  	this.dialogRef.close(res);
  }

  Aceptar():void
  {
    this.Mensajes = [];

    //Validar Nombre
    if(this.User.Name == null || this.User.Name == "")
    {
      this.Mensajes.push(
      {
        Type : MessageTypes.Warning,
        Message : "El usuario debe tener un nombre."
      });
    }

    //Validar Genero
    if(this.User.Sexo == null || this.User.Sexo == undefined)
    {
      this.Mensajes.push(
      {
        Type : MessageTypes.Warning,
        Message : "Selecciona un genero para el usuario."
      });
    }

    //Validar Usuario
    if(this.User.User == null || this.User.User == "")
    {
      this.Mensajes.push(
      {
        Type : MessageTypes.Warning,
        Message : "El usuario no puede estar vacío."
      });
    }

    //Validar Password
    if(this.User.Password == null || this.User.Password == "")
    {
      this.Mensajes.push(
      {
        Type : MessageTypes.Warning,
        Message : "Establece una contraseña para el usuario."
      });
    }

    //Validar Email
    if(this.Email.Address != null && this.Email.Address != "")
    {
      var mailformat = /^\w+([\.-]?\w+)*@\w+([\.-]?\w+)*(\.\w{2,3})+$/;
      if(!this.Email.Address.match(mailformat))
      {
        this.Mensajes.push(
        {
          Type : MessageTypes.Warning,
          Message : "El formato del correo es incorrecto."
        });
      }
    }

    if(this.Mensajes.length > 0)
    {
      this.Mensajes = this.Mensajes.slice(0, 1);
    }
    else
    {
      /*this.Mensajes.push(
      {        Type : MessageTypes.Success,
        Message : "todo fine :D."
      });*/
      this.Close({user:this.User, email: this.Email});
    }
  }

}
