import { Component, OnInit, ElementRef, ViewChild, Input } from '@angular/core';

export interface Data
{
	Titulo : string;
	Valor : any;
	Segmentos : Array<Segment>;
}

interface Segment
{
	Titulo : string;
	Porcentaje : number;
	Valor: any;
	Selected : boolean;
}

@Component({
  selector: 'app-donut-chart',
  templateUrl: './donut-chart.component.html',
  styleUrls: ['./donut-chart.component.css']
})

export class DonutChartComponent implements OnInit {
	@ViewChild('Valor', { static: false }) public ValorElement: ElementRef;
	@ViewChild('Titulo', { static: false }) public TituloElement: ElementRef;

	@Input() Data : Data = 
	{
		Titulo : undefined,
		Valor: 0,
		Segmentos: []
	};

	Pi : number = 3.1416;
	Fondo : string = '';
	Radius : number = 81;
	strokeWidth : number = 10;
	Perimetro : number = 2 * this.Pi * (this.Radius - (this.strokeWidth/2));
	Perimetro2 : number = 2 * this.Pi * (this.Radius - (this.strokeWidth));

  constructor() { }

  ngOnInit() 
  {
  	if(this.Data==null)
  	{
  		this.Data = {
			Titulo : undefined,
			Valor: 0,
			Segmentos: []
		};
  	}
  }

  Select(Segmento : Segment, selected : boolean)
  {
  	Segmento.Selected = selected;

  	if(Segmento.Selected)
  	{
  		this.ValorElement.nativeElement.innerHTML = Segmento.Valor;
  		this.TituloElement.nativeElement.innerHTML = Segmento.Titulo;
  	}
  	else
  	{
  		this.ValorElement.nativeElement.innerHTML = this.Data.Valor;
  		this.TituloElement.nativeElement.innerHTML = this.Data.Titulo;
  	}
  }

  getDashoffset(Segmento: Segment, x) : number
  {
  	let suma : number = 0;

  	let index : number = this.Data.Segmentos.indexOf(Segmento);

  	for (var s = 0 ; s < index; s++) 
  	{
  		let Porcentaje : number = this.Data.Segmentos[s].Porcentaje;
  		suma += Porcentaje;
  	}

  	return ((((x==1)?this.Perimetro:this.Perimetro2)/100) * ((suma > 0 ? (100 - suma) : 0) + 25)) /*- ( index > -1 ? (this.strokeWidth/5) : 0)*/;
  	//((Perimetro/100) * getDashoffset(segmento))
  	//((100 - 50) + 25)) - ((strokeWidth/2) * 3)
  }

  getColor(index):string
  {
  	var iluminacion = 0;

  	let iluminacion1 : Array<number> = [65, 75];
  	let iluminacion2 : Array<number> = [65, 75, 55];

  	if(this.Data.Segmentos.length % 2 == 0)
  	{
  		let n = index;
  		while(n > iluminacion1.length-1)
  		{
  			n -= iluminacion1.length;
  		}
  		iluminacion = iluminacion1[n];
  	}
  	else
  	{
  		let n = index;
  		while(n > iluminacion2.length-1)
  		{
  			n -= iluminacion2.length;
  		}
  		iluminacion = iluminacion2[n];
  	}

  	return 'hsl(208, 31%, ' + iluminacion + '%)';
  }
}
