import {SelectionModel} from '@angular/cdk/collections';
import {FlatTreeControl} from '@angular/cdk/tree';
import {Injectable} from '@angular/core';
import {MatTreeFlatDataSource, MatTreeFlattener} from '@angular/material/tree';
import {BehaviorSubject} from 'rxjs';
import { Component, OnInit, ViewChild, Output, EventEmitter} from '@angular/core';
import { Meses } from '../Enum/Meses';

/**
 * Food data with nested structure.
 * Each node has a name and an optiona list of children.
 */
interface FoodNode {
  name: string;
  children?: FoodNode[];
  indeterminate:boolean;
  fecha?:Date;
}

const TREE_DATA: FoodNode[] = [
  {
    name: 'Fruit',
    children: [
      {name: 'Apple', indeterminate:false},
      {name: 'Banana', indeterminate:false},
      {name: 'Fruit loops', indeterminate:false},
    ],
    indeterminate:false
  }, {
    name: 'Vegetables',
    children: [
      {
        name: 'Green',
        children: [
          {name: 'Broccoli', indeterminate:false},
          {name: 'Brussel sprouts', indeterminate:false},
        ], indeterminate:false
      }, {
        name: 'Orange',
        children: [
          {name: 'Pumpkins', indeterminate:false},
          {name: 'Carrots', indeterminate:false},
        ], indeterminate:false
      },
    ], indeterminate:false
  },
];

const TREE_DATA2: FoodNode[] = [
  {
    name: 'Fruit', indeterminate:false
  }, {
    name: 'Vegetables', indeterminate:false
  },
];

/** Flat node with expandable and level information */
interface ExampleFlatNode {
  expandable: boolean;
  name: string;
  level: number;
  fecha: Date;
}

@Component({
  selector: 'app-test',
  templateUrl: './test.component.html',
  styleUrls: ['./test.component.css']
})
export class TestComponent {

	checklistSelection = new SelectionModel<ExampleFlatNode>(true);
	getLevel = (node: ExampleFlatNode) => node.level;

	datos: FoodNode[] = [];

	fechas = 
	[
		{ fecha: new Date(2019, 8, 1) },
		{ fecha: new Date(2019, 10, 2) },
		{ fecha: new Date(2019, 8, 1) },
		{ fecha: new Date(2019, 11, 13) },
		{ fecha: new Date(2019, 9, 1) },
		{ fecha: new Date(2018, 9, 10) },
		{ fecha: new Date(2019, 11, 1) },
		{ fecha: new Date(2018, 10, 1) }
	];

	private _transformer = (node: FoodNode, level: number) => {
    return {
      expandable: !!node.children && node.children.length > 0,
      name: node.name,
      level: level,
      fecha: node.fecha
    };
  }

  treeControl = new FlatTreeControl<ExampleFlatNode>(
      node => node.level, node => node.expandable);

  treeFlattener = new MatTreeFlattener(
      this._transformer, node => node.level, node => node.expandable, node => node.children);

  dataSource = new MatTreeFlatDataSource(this.treeControl, this.treeFlattener);

  constructor() {
    //this.dataSource.data = TREE_DATA;
   	this.cargarFechas();
   	this.dataSource.data = this.datos;
   	console.log(this.dataSource.data);
  }

  hasChild = (_: number, node: ExampleFlatNode) => node.expandable;

  escribir(cosa):string
  {
  	return JSON.stringify(cosa);
  }

  isSelected(nodo: ExampleFlatNode):boolean
  {
  	return this.checklistSelection.isSelected(nodo);
  }

  SeleccionarNodo(nodo: ExampleFlatNode)
  {
  	this.checklistSelection.toggle(nodo);
  	this.checkAllParentsSelection(nodo);

    this.todos();
  }

  checkAllParentsSelection(node: ExampleFlatNode): void {
    let parent: ExampleFlatNode | null = this.getParentNode(node);
    while (parent) {
      this.checkRootNodeSelection(parent);
      parent = this.getParentNode(parent);
    }
  }

  getParentNode(node: ExampleFlatNode): ExampleFlatNode | null {
    const currentLevel = this.getLevel(node);

    if (currentLevel < 1) {
      return null;
    }

    const startIndex = this.treeControl.dataNodes.indexOf(node) - 1;

    for (let i = startIndex; i >= 0; i--) {
      const currentNode = this.treeControl.dataNodes[i];

      if (this.getLevel(currentNode) < currentLevel) {
        return currentNode;
      }
    }
    return null;
  }

  checkRootNodeSelection(node: ExampleFlatNode): void {
    const nodeSelected = this.checklistSelection.isSelected(node);
    const descendants = this.treeControl.getDescendants(node);
    const descAllSelected = descendants.every(child =>
      this.checklistSelection.isSelected(child)
    );
    if (nodeSelected && !descAllSelected) {
      this.checklistSelection.deselect(node);
    } else if (!nodeSelected && descAllSelected) {
      this.checklistSelection.select(node);
    }
  }

  descendantsAllSelected(node: ExampleFlatNode): boolean {
    const descendants = this.treeControl.getDescendants(node);
    const descAllSelected = descendants.every(child =>
      this.checklistSelection.isSelected(child)
    );
    return descAllSelected;
  }

  todoItemSelectionToggle(node: ExampleFlatNode): void {
    this.checklistSelection.toggle(node);
    const descendants = this.treeControl.getDescendants(node);
    this.checklistSelection.isSelected(node)
      ? this.checklistSelection.select(...descendants)
      : this.checklistSelection.deselect(...descendants);

    // Force update for the parent
    descendants.every(child =>
      this.checklistSelection.isSelected(child)
    );
    this.checkAllParentsSelection(node);

    this.todos();
  }

  descendantsPartiallySelected(node: ExampleFlatNode): boolean {
    const descendants = this.treeControl.getDescendants(node);
    const result = descendants.some(child => this.checklistSelection.isSelected(child));
    return result && !this.descendantsAllSelected(node);
  }

  todos()
  {
    var fechas = this.checklistSelection.selected.filter(n => n.level == 2).map( n => n.fecha);
    console.clear();
    console.log(fechas);
  }
  
  countVisibles():number
  {
    var v = 0;
    var w = this.treeControl.dataNodes.filter( d => d.level == 0).length;
    var x = this.treeControl.dataNodes.filter( d => this.treeControl.isExpanded(d));
    var y = x.map( n => this.countHijos(n));
    if ( y.length > 0) var z = y.reduce(this.reducer);
    z = (z) ? z : 0;
    return (w + z);
  }

  reducer = (accumulator, currentValue) => accumulator + currentValue;

  countHijos(nodo:ExampleFlatNode):number
  {
    var y = this.treeControl.getDescendants(nodo);
    y = y.filter(n => n.level == nodo.level + 1);
    return y.length;
  }

  cargarFechas()
  {
  	var fechas = this.fechas.map(f => { return { year: f.fecha.getFullYear(), month: f.fecha.getMonth(), day: f.fecha.getDate()}});
  	fechas = fechas.sort((a, b) => (a.day < b.day) ? -1 : 1);
  	fechas = fechas.sort((a, b) => (a.month < b.month) ? -1 : 1);
  	fechas = fechas.sort((a, b) => (a.year < b.year) ? -1 : 1);

  	for (var f =  0; f < fechas.length; f++) 
  	{
  		var fecha = fechas[f];

  		var nodoy = this.datos.filter(n => n.name == fecha.year.toString())[0]; 
  		if(nodoy == null)
  		{
  			this.datos.push(
  			{
  				name: fecha.year.toString(),
  				children: [], 
  				indeterminate:false
  			});

  			nodoy = this.datos[this.datos.length - 1]; 
  		}

  		var nodom = nodoy.children.filter(n => n.name == Meses[fecha.month])[0];
  		if(nodom == null)
  		{
  			nodoy.children.push(
  			{
  				name: Meses[fecha.month],
  				children: [],
  				indeterminate:false
  			});

  			nodom = nodoy.children[nodoy.children.length - 1];
  		}

  		var nodod = nodom.children.filter(n => n.name == fecha.day.toString())[0];
  		if(nodod == null)
  		{
  			nodom.children.push(
  			{
  				name: fecha.day.toString(),
  				indeterminate:false, 
  				fecha:new Date(fecha.year, fecha.month, fecha.day)
  			})
  		}
  	}
  }
}