import { Component, OnInit } from '@angular/core';
import { FormatNumber } from '../Funciones'

@Component({
  selector: 'app-dashboard',
  templateUrl: './dashboard.component.html',
  styleUrls: ['./dashboard.component.css']
})
export class DashboardComponent implements OnInit {

	FormatNumber = new FormatNumber();
	opciones : Array<any> = 
	[
		{
			Titulo:"Ingresos", 
			Valor:"$0",
			selected:true
		},
		/*{
			Titulo:"Ocupaciones", 
			Valor:"0",
			selected:false
		},*/
		{
			Titulo:"Tickets", 
			Valor:"0",
			selected:false
		}/*,
		{
			Titulo:"Pensiones", 
			Valor:"0",
			selected:false
		}*/
	];

	ChangeIngresos(event):void
	{
		var opcion = this.opciones.filter(o => o.Titulo == "Ingresos")[0];
		opcion.Valor = this.FormatNumber.nuevo(event.Total, '$');
	}

	ChangeOcupaciones(event):void
	{
		var opcion = this.opciones.filter(o => o.Titulo == "Ocupaciones")[0];
		opcion.Valor = this.FormatNumber.nuevo(event.Total, '');
	}

	changeSelection(opcion):void
	{
		var r = this.opciones.filter(o => o.selected == true);
		for (var o = r.length - 1; o >= 0; o--) {
			r[o].selected = false;
		}

		opcion.selected = true;
		//localStorage.setItem("cargando",true);
	}

	isSelected(nombre):boolean
	{
		var r = this.opciones.filter(o => o.selected == true);
		return r[0].Titulo == nombre;
	}

  constructor() { }

  ngOnInit() {
  }

}
