import { Component, OnInit, ViewChild, ElementRef } from '@angular/core';
import { RestService } from '../../../rest.service';
import { LoadingController } from '../../../Controllers/LoadingController';
import { Data } from '../../../Charts/area-chart/area-chart.component';
import { estadisticas, Meses } from '../../../Models/Interfaces';


@Component({
  selector: 'app-afluencia',
  templateUrl: './afluencia.component.html',
  styleUrls: ['./afluencia.component.css']
})
export class AfluenciaComponent implements OnInit {

  constructor(private LoadingController : LoadingController, private rest : RestService) { }
  @ViewChild('DaysChart', { static: false }) public DaysChart: ElementRef;

  Entradas : Array<Data> = [];
  Salidas : Array<Data> = [];

  Meses = Meses;

  DaysChartAncho : number = 200;
  DaysChartAlto : number = 200;

  EstadisticasE : Array<estadisticas> = []; 
  EstadisticasS : Array<estadisticas> = []; 

  calendario = 
  {
  	hide : true,
  	fecha : new Date(),
  	top : 100,
  	left : 100
  }

  NombreDias : Array<any> =
  [
    { valor: 0, nombre: "Dom"},
    { valor: 1, nombre: "Lun"},
    { valor: 2, nombre: "Mar"},
    { valor: 3, nombre: "Mié"},
    { valor: 4, nombre: "Jue"},
    { valor: 5, nombre: "Vie"},
    { valor: 6, nombre: "Sáb"}
  ];

  ngOnInit() {
  	this.CargarTemporales();
  	this.CargarEstadisticas();
  }

  ngAfterViewInit() 
  {
  	setTimeout(() => {
  		this.DaysChartAncho = this.DaysChart.nativeElement.offsetWidth - 20;
		this.DaysChartAlto = this.DaysChart.nativeElement.offsetHeight - 100;
	});
  }

  GetShortDate(fecha : Date) : string
  {
    let f:Date = new Date(fecha);
    return this.NombreDias[f.getDay()].nombre + "., " + f.getDate() + " " + this.Meses[f.getMonth()].NombreCorto + ". "+ f.getFullYear();
  }

  GetTime(fecha: Date) : string
  {
    let f:Date = new Date(fecha);
    return f.toLocaleString('en-US', { hour: 'numeric', minute: 'numeric', hour12: true });
  }

  CargarTemporales()
  {
    for (var d = 1; d <= 24; d++) { this.Entradas.push({valueX:0, valueY: d.toString(), minValueX:0, maxValueX: 0, selected: false}) }
	for (var d = 1; d <= 24; d++) { this.Salidas.push({valueX:0, valueY: d.toString(), minValueX:0, maxValueX: 0, selected: false}) }
  }

	MostrarCalendario(e)
	{
		this.calendario.left = e.screenX;
		this.calendario.top = e.screenY;
		this.calendario.hide = !this.calendario.hide;
	}

	CerrarCalendario()
	{
		this.calendario.hide = true;
	}

	AplicarFechas(fechas)
	{
		let fecha : Date = fechas.fechaI;
		this.calendario.fecha = fecha;
		this.CalcularHoras(fecha.getFullYear(), this.Meses.find(m => m.Valor == fecha.getMonth()).Number, fecha.getDate());
		this.calendario.hide = true;
	}

  CargarEstadisticas()
  {
  	this.LoadingController.Mostrar();
  	this.rest.EntradasPorHora().subscribe((data : any) =>
  	{
  		this.EstadisticasE = data.report.filter(r => r.tipo == 'ENT').map(r => 
		{
			let estadistica : estadisticas = 
			{
				Suma : r.suma,
				Minimo : 0,
				Maximo : 0,
				Year : r.year,
				Month : r.month,
				Day : r.day,
				WeekDay : 0,
				Hour : r.hour
			};
			return estadistica;
		});

		this.EstadisticasS = data.report.filter(r => r.tipo == 'SLD').map(r => 
		{
			let estadistica : estadisticas = 
			{
				Suma : r.suma,
				Minimo : 0,
				Maximo : 0,
				Year : r.year,
				Month : r.month,
				Day : r.day,
				WeekDay : 0,
				Hour : r.hour
			};
			return estadistica;
		});

		var fecha = new Date();
  		this.CalcularHoras(fecha.getFullYear(), this.Meses.find(m => m.Valor == fecha.getMonth()).Number, fecha.getDate())
  		this.LoadingController.Cerrar(data.message);
    },
    (ex) =>
    {
      var mensaje = 
      {
        mostrar: true,
        mensaje: ex.error.message,
        status: 1
      };

      localStorage.setItem("mensaje",JSON.stringify(mensaje));
      this.LoadingController.Cerrar(ex.statusText);
    });
  }

  CalcularHoras(Año, Mes, Dia)
  {
  	this.Entradas = [];
  	this.Salidas = [];

  	var tmp = this.EstadisticasE.filter(e => e.Year == Año && e.Month == Mes && e.Day == Dia).sort((a, b) => (a.Suma > b.Suma) ? -1 : (a.Suma < b.Suma) ? 1 : 0);
  	let fraccion : number = 6;

  	for(var h = 0 ; h < 24 ; h ++)
  	{
  		var tmp2 = this.EstadisticasE.filter(e => e.Hour == h).sort((a, b) => (a.Suma > b.Suma) ? -1 : (a.Suma < b.Suma) ? 1 : 0);
  		let maxarray : Array<estadisticas> = tmp2.slice(0, tmp2.length / fraccion);
	    let minarray : Array<estadisticas> = tmp2.slice((tmp2.length / fraccion) * (fraccion - 1), tmp2.length);

	    let maximo : number = maxarray.length > 0 ? maxarray.map(d => d.Suma).reduce((actual, acumulado)=> actual + acumulado) / maxarray.length : 0;
	    let minimo : number = minarray.length > 0 ? minarray.map(d => d.Suma).reduce((actual, acumulado)=> actual + acumulado) / minarray.length : 0;

  		var dato = tmp.find(e => e.Hour == h);
	  	this.Entradas.push(
	  	{
  			valueX : dato != null ? dato.Suma : 0,
  			valueY : h.toString(),
  			maxValueX : maximo,
  			minValueX : minimo,
  			selected : false
  		});
  	}

  	var tmp = this.EstadisticasS.filter(e => e.Year == Año && e.Month == Mes && e.Day == Dia).sort((a, b) => (a.Suma > b.Suma) ? -1 : (a.Suma < b.Suma) ? 1 : 0);;
  	for(var h = 0 ; h < 24 ; h ++)
  	{
  		var tmp2 = this.EstadisticasS.filter(e => e.Hour == h).sort((a, b) => (a.Suma > b.Suma) ? -1 : (a.Suma < b.Suma) ? 1 : 0);
  		let maxarray : Array<estadisticas> = tmp2.slice(0, tmp2.length / fraccion);
	    let minarray : Array<estadisticas> = tmp2.slice((tmp2.length / fraccion) * (fraccion - 1), tmp2.length);

	    let maximo : number = maxarray.length > 0 ? maxarray.map(d => d.Suma).reduce((actual, acumulado)=> actual + acumulado) / maxarray.length : 0;
	    let minimo : number = minarray.length > 0 ? minarray.map(d => d.Suma).reduce((actual, acumulado)=> actual + acumulado) / minarray.length : 0;

  		var dato = tmp.find(e => e.Hour == h);
	  	this.Salidas.push(
	  	{
  			valueX : dato != null ? dato.Suma : 0,
  			valueY : h.toString(),
  			maxValueX : maximo,
  			minValueX : minimo,
  			selected : false
  		});
  	}
  }

}
