import { Component, OnInit, ViewChild } from '@angular/core';
import { RestService } from '../rest.service';
import { User } from '../Models/User';
import { Group } from '../Models/Group';
import { Sexo } from '../Enum/Sexo';
import { UserModalComponent, UserModalData } from '../user-modal/user-modal.component';
import { PermisosIndividualesComponent, PermisosIndividualesData, PermisosIndividualesMode } from '../permisos-individuales/permisos-individuales.component';
import { LoadingController } from '../Controllers/LoadingController';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { WorkModes } from '../Enum/WorkModes';
import { MatTableDataSource, MatSort, MatPaginator } from '@angular/material';
import { Filtro } from '../Models/Interfaces';
import { YesNoDialogComponent, YesNoDialogData, Color } from '../Views/Dialog/yes-no-dialog/yes-no-dialog.component';
import { MessageTypes } from '../Enum/MessageTypes';
import { MessageController } from '../Controllers/MessageController';
import { MessageType } from '../Views/Dialog/message/message.component';
import { Modulo, Permiso } from '../Models/Modulo';
import { PermisosController } from '../Controllers/PermisosController';


@Component({
  selector: 'app-usuarios',
  templateUrl: './usuarios.component.html',
  styleUrls: ['./usuarios.component.css']
})
export class UsuariosComponent implements OnInit {

	//Usuarios : User[] = [];
	zoom = false;
	tableFilters : Array<Filtro>  = [];
	Busqueda : string = "";

	@ViewChild(MatPaginator, {static: true}) paginator: MatPaginator;
	@ViewChild(MatSort, {static: true}) sort: MatSort;

	displayedColumns: string[] = ['Imagen', 'User', 'Name', 'Grupo.Name', 'Opciones'];
  	dataSource : MatTableDataSource<User>;

  	vistas = 
	[
		{ valor : 1, nombre : "list" },
		{ valor : 2, nombre : "view_module" }
	];

	vista;

  	constructor(
  		private rest : RestService, 
  		public dialog: MatDialog, 
  		private LoadingController: LoadingController, 
  		private MessageController : MessageController,
  		private PermisosController : PermisosController
  		) { }

  	ngOnInit() 
  	{
  		this.CargarUsuarios();
  		//this.CargarNotificaciones();
  	}

  	CargarNotificaciones() : void
  {
    let usuario = localStorage.getItem('Usuario');
    if(usuario)
    {
      let Id : number = JSON.parse(usuario).Id;
      this.rest.GetNotificaciones(Id).subscribe((result : any)=>
      {
        if(result.success)
        {
          this.MessageController.Mostrar(result.message, MessageType.Ok);
        }
        else
        {
          this.MessageController.Mostrar(result.message, MessageType.Error);
        }
        console.log(result);
      },
      (ex)=>
      {
        this.MessageController.Mostrar(ex.message, MessageType.Error);
        console.log(ex);
      })
    }
  }

  	ngAfterContentInit()
	{
		setTimeout(() => 
		{
			this.vista = 2;
		});
	}

	Permisos(usuario : User): void
	{
		this.LoadingController.Mostrar();
		this.rest.Permisos().subscribe((result:any)=>
		{
			if(result.success)
			{
				var tmp = result.Permisos.filter((v, i, a) => a.indexOf(a.find(r => r.id == v.id)) == i);

				let permisos : Array<Modulo> = tmp.map(v => 
				{
					let Modulo : Modulo = 
					{
						Id : v.id,
						Name : v.modulo,
						Permisos : result.Permisos.filter(r => r.id == v.id).map(r =>
						{
							let Permiso : Permiso = 
							{
								Id : r.idsm,
								Name : r.submodulo,
								Tag : r.tag,
								Valor : (r.status == "1")
							} 

							return Permiso;
						})
					}
					return Modulo;
				});
				
				let data : PermisosIndividualesData = 
				{
					Modulos : permisos,
					User : usuario,
					Group : null,
					WorkMode : PermisosIndividualesMode.Usuario,
					Permisos : []
				}

				this.rest.PermisosUsuario(usuario.Id).subscribe((result:any)=>
				{
					this.LoadingController.Cerrar(result.message);

					if(result.success)
					{
						data.Permisos = result.Permisos;

			      		const dialogRef = this.dialog.open(PermisosIndividualesComponent, 
						{
							width: '700px',
							disableClose: true,
							data: data
						});

						dialogRef.afterClosed().subscribe((res : any)=>
						{
							if(res != null)
      						{
				                let result : Array<{Tag : string, Valor : boolean}> = res;

				                this.LoadingController.Mostrar();
				                this.rest.PermisoUsuario(result, usuario.Id).subscribe((result : any)=>
				                {
				                  this.LoadingController.Cerrar(result.message);
				                  if(result.success)
				                  {
				                    this.MessageController.Mostrar(result.message, MessageType.Ok);
				                  }
				                  else
				                  {
				                    this.MessageController.Mostrar(result.message, MessageType.Error);
				                  }
			                	},
				                (ex)=>
				                {
				                  this.LoadingController.Cerrar(ex.statusText);
				                  this.MessageController.Mostrar(ex.error.message, MessageType.Error);
				                });
			              	}
						},
						(ex)=>
						{
							this.LoadingController.Cerrar(ex.statusText);
					      	this.MessageController.Mostrar(ex.error.message, MessageType.Error);
						});
			      	}
			      	else
			      	{
			      		this.MessageController.Mostrar(result.message, MessageType.Error);
			      	}
		      	},
				(ex)=>
				{
					this.LoadingController.Cerrar(ex.statusText);
			      	this.MessageController.Mostrar(ex.error.message, MessageType.Error);
				});
	      	}
	      	else
	      	{
	      		this.MessageController.Mostrar(result.message, MessageType.Error);
	      	}
		},
		(ex)=>
		{
			this.LoadingController.Cerrar(ex.statusText);
	      	this.MessageController.Mostrar(ex.error.message, MessageType.Error);
		});
	}

	Eliminar(usuario : User): void
	{
		let data : YesNoDialogData =
	    {
	      Titulo : "¿Eliminar Usuario?",
	      Mensaje : "El usuario " + usuario.Name + " (" + usuario.User + ") será eliminada del sistema.",
	      Accion : "Eliminar",
	      Color : Color.Rojo,
	      Information : [
	      {
	        Message : "Esta acción no se puede deshacer.",
	        Type : MessageTypes.Warning
	      }]
	    };

	    const dialogRef = this.dialog.open(YesNoDialogComponent, 
	    {
	      width: '500px',
	      disableClose: false,
	      data: data
	    });

	    dialogRef.afterClosed().subscribe((result : any)=>
	    {
	    	if(result==true)
			{
				this.LoadingController.Mostrar();
				this.rest.DeleteUser(usuario.Id).subscribe((result : any) =>
				{
					this.dataSource.data = this.dataSource.data.filter(u => u != usuario);
					this.LoadingController.Cerrar(result.message);
				},
				(ex)=>
				{
					var mensaje = 
					{
						mostrar: true,
						mensaje: ex.error.message,
						status: 1
					};

					localStorage.setItem("mensaje",JSON.stringify(mensaje));
					this.LoadingController.Cerrar(ex.statusText);
				});
			}
	    },
	    (ex)=>
	    {
	      var mensaje = 
	      {
	        mostrar: true,
	        mensaje: ex.error.message,
	        status: 1
	      };

	      localStorage.setItem("mensaje",JSON.stringify(mensaje));
	      this.LoadingController.Cerrar(ex.statusText);
	    });
	}

	Agregar(): void
	{
		const dialogRef = this.dialog.open(UserModalComponent, 
		{
			width: '700px',
			disableClose: true,
			data: 
			{
			    Modo : WorkModes.Add
			}
		});

		dialogRef.afterClosed().subscribe(result =>
		{
			if(result != null)
			{
				this.LoadingController.Mostrar();
				let user : User = result.user;
				this.rest.InsertUser(user, result.email).subscribe((result:any)=>
				{
		      		this.LoadingController.Cerrar(result.message);

					if(result.success)
					{
						this.CargarUsuarios();
			      		this.MessageController.Mostrar(result.message, MessageType.Ok);
			      	}
			      	else
			      	{
			      		this.MessageController.Mostrar(result.message, MessageType.Error);
			      	}
				},
				(ex)=>
				{
					this.LoadingController.Cerrar(ex.statusText);
			      	this.MessageController.Mostrar(ex.error.message, MessageType.Error);
				});
			}
		})
	}

  	Editar(user : User):void
	{
		let IdPerson : number;

		this.LoadingController.Mostrar();
		this.rest.getIdPerson(user.Id).subscribe((result : any) =>
		{
			if(result.success == true)
			{
				IdPerson = result.IdPerson;

				this.rest.Contact(IdPerson).subscribe((result : any) =>
				{
					this.LoadingController.Cerrar(result.message);
					if(result.success == true)
					{
						let data : UserModalData =  
						{
				    		User : user,
						    Modo : WorkModes.Update,
						    Email : result.Contact != null ?
						    ( result.Contact.emails.length > 0 ?
						    { Id : result.Contact.emails[0].id, Address : result.Contact.emails[0].address } : 
						    { Id : null, Address : "" }) : { Id : null, Address : "" }
						};

						const dialogRef = this.dialog.open(UserModalComponent, 
						{
							width: '700px',
							disableClose: true,
							data: data
						});

					    dialogRef.afterClosed().subscribe((result : any) => 
					    {
					    	if(result!=null)
					    	{
						    	this.LoadingController.Mostrar();

						    	let newUser : User = result.user;

						    	this.rest.UpdateUser(newUser, result.email, IdPerson).subscribe((result : any) =>
								{
									this.LoadingController.Cerrar(result.message);
									if(result.success == true)
									{
										user.Id = newUser.Id;
										user.Name = newUser.Name;
										user.ActiveTerminal = newUser.ActiveTerminal;
										user.Sexo = newUser.Sexo;
										user.RFC = newUser.RFC;
										user.Grupo = newUser.Grupo;
										user.Password = newUser.Password;
										user.User = newUser.User;
										user.Status = newUser.Status;
										user.Url = newUser.Url;
										user.Correos = newUser.Correos;

										this.MessageController.Mostrar(result.message, MessageType.Ok);
									}
									else
									{
										this.MessageController.Mostrar(result.message, MessageType.Error);
									}
								},
								(ex)=>
								{
							      this.LoadingController.Cerrar(ex.statusText);
							      this.MessageController.Mostrar(ex.error.message, MessageType.Error);
								});
		    				}
					    });
					}
				},
				(ex)=>
				{
			      this.LoadingController.Cerrar(ex.statusText);
			      this.MessageController.Mostrar(ex.error.message, MessageType.Error);
				});
			}
			else
			{
				this.LoadingController.Cerrar(result.message);
			}
		},
		(ex)=>
		{
	      this.LoadingController.Cerrar(ex.statusText);
	      this.MessageController.Mostrar(ex.error.message, MessageType.Error);
		});
	}

  	cambiarzoom():void
  	{
  		this.zoom = !this.zoom;
  	}

  	Buscar():void
	{
	    var filtro = this.tableFilters.filter(tf => tf.id == 'Busqueda')[0];
	  	var f = this.Busqueda;

	  	if(filtro == null || filtro == undefined)
	  	{
	  		this.tableFilters.push(
	  		{
	  			id: 'Busqueda',
	  			values: f
	  		});
	  	}

	  	filtro = this.tableFilters.filter(tf => tf.id == 'Busqueda')[0];
	  	filtro.values = f;

	  	if(f == "" || f == null)
	  	{
	  		this.tableFilters = this.tableFilters.filter(f => f.id != 'Busqueda');
	  	}

	  this.AplicarFiltros();
  	}

  	AplicarFiltros()
  	{
  		this.dataSource.filter = JSON.stringify(this.tableFilters);
    	if (this.dataSource.paginator) 
    	{
      		this.dataSource.paginator.firstPage();
    	}
  	}

  	CargarUsuarios()
  	{
  		let result : Array<User> = [];
  		this.LoadingController.Mostrar();
  		this.rest.getActiveUsers().subscribe((data : any) =>
    	{
    		if(data.success == true)
    		{
	    		result = data.users.map(u =>
				{ 
					let user : User =
					{
						ActiveTerminal: u.activeTerminal,
						Sexo: u.gender,
						RFC: u.rfc,
						Grupo: (u.group != null)? 
						{
							Id: u.group.id,
							Name: u.group.value,
							Icon: u.group.icon,
							Description: u.group.description,
							Status: u.group.status
						}: null,
						Password: u.password,
						Name: u.name,
						User: u.value,
						Id: u.id,
						Status: u.status,
						Url: "assets/svg/profile.svg",
						Correos : null
					};

					return user;
				});

	  			this.dataSource = new MatTableDataSource(result);
			  	this.dataSource.sort = this.sort;
			  	this.dataSource.paginator = this.paginator;
			  	this.dataSource.sortingDataAccessor = (data, sortHeaderId: string) => 
			    {
			        switch (sortHeaderId) 
			        {
			        	case 'Grupo.Nombre':
			        		return (data.Grupo!=null) ? data.Grupo.Name : null;
			        	default:
			        	return this.getPropertyByPath(data, sortHeaderId);
			        	break;
			        }
		      	};

		      	this.dataSource.filterPredicate = (data, filtersJson: string) => 
		      	{
		      		var match = true;
			        const matchFilter = [];
			        const filters : Array<Filtro> = JSON.parse(filtersJson);

			        filters.forEach(filter => 
			        {
						switch (filter.id) 
						{
				            case "Busqueda":
				              match = match && (data.Name.toLowerCase().includes(filter.values.toLowerCase()) || data.User.toLowerCase().includes(filter.values.toLowerCase()));
							break;
				            
				            default:
				              //match = match && val.toString().toLoweCase().includes(filter.value.toLowerCase());
				            break;
			          }
			          matchFilter.push(match);
			        });
			        return matchFilter.every(Boolean);
				};
			}

    		this.LoadingController.Cerrar(data.message);
    	},
	    (ex) =>
	    {
	      this.LoadingController.Cerrar(ex.statusText);
	      this.MessageController.Mostrar(ex.error.message, MessageType.Error);
	    });
  	}

 	getPropertyByPath(obj: Object, pathString: string)
  	{
  		return pathString.split('.').reduce((o, i) => o[i], obj);
	}
}
