import { Component, OnInit, ElementRef, ViewChild } from '@angular/core';
import {NgbModal, ModalDismissReasons} from '@ng-bootstrap/ng-bootstrap';

@Component({
  selector: 'app-mapa',
  templateUrl: './mapa.component.html',
  styleUrls: ['./mapa.component.css']
})

export class MapaComponent implements OnInit {

  closeResult: string;
  /* divID: string; */
  ocupado: boolean;

  @ViewChild('divID', {static: true}) divID: ElementRef;

  ngAfterViewInit(){

    console.log(this.divID.nativeElement[0]);
    this.divID.nativeElement.innerHTML = "Hello Angular 10!";

  }

  constructor(private modalService: NgbModal, divID: ElementRef) {}

  open(content, divEl) {

    this.divID = divEl.id;
    console.log(divEl.id);
    console.log(divEl)
    this.modalService.open(content, {ariaLabelledBy: 'modal-basic-title', 
                                      backdrop: 'static',
                                      size: 'md', 
                                      keyboard: false, 
                                      centered: true
  }).result.then((result) => {

      this.closeResult = `Closed with: ${result}`;

    }, (reason) => {

      this.closeResult = `Dismissed ${this.getDismissReason(reason)}`;

    });

  }

  private getDismissReason(reason: any): string {

    if (reason === ModalDismissReasons.ESC) {

      return 'by pressing ESC';

    } else if (reason === ModalDismissReasons.BACKDROP_CLICK) {

      return 'by clicking on a backdrop';

    } else {

      return  `with: ${reason}`;

    }

  }

  ngOnInit() {
    

  }

}
